
// NOTE(dbildstr): single quote should be quoted, due to it being treated
// as the beginning of a quoted string in jsonrpc calls otherwise
const quoteChars = [
    '#',
    '"',
    ' ',
    '{',
    '}',
    '\'',
    '\\',
    '\n',
    '\r',
    '\t',
];

function quoteKey(key) {
    if (Number.isFinite(key)) {
        return key;
    }
    let needQuote = false;
    const escapeChars = [
        '\\',
        '"',
    ];
    const str = key.split('').reduce((buf, char) => {
        if (quoteChars.includes(char)) {
            needQuote = true;
        }
        if (escapeChars.includes(char)) {
            buf.push('\\');
        }
        buf.push(char);
        return buf;
    }, []).join('');
    if (needQuote || str === '') {
        return `"${str}"`;
    }
    return str;
}

function unquoteKey(key) {
    if (typeof key !== 'string') {
        return key;
    }

    if (key.length < 2 || !key.startsWith('"') || !key.endsWith('"')) {
        return key;
    }
    const chars = key.substring(1, key.length - 1).split('');
    if (chars.length === 0) {
        return '';
    }
    const wasEscaped = quoteChars.some(c => chars.includes(c));
    if (!wasEscaped) {
        return key;
    }
    let inEscape = false;
    let inQuote = false;
    const str = chars.reduce((buf, char) => {
        if (inEscape) {
            inEscape = false;
            buf.push(char);
        } else if (char === '\\') {
            inEscape = true;
        } else if (inQuote) {
            if (char === '"') {
                inQuote = false;
            } else {
                buf.push(char);
            }
        } else if (char === '"') {
            inQuote = true;
        } else {
            buf.push(char);
        }
        return buf;
    }, []);
    return str.join('');
}

const wrappedValue = (initialValue) => {
    let val = initialValue;
    return (newValue) => {
        if (newValue !== undefined) {
            val = newValue;
        }
        return val;
    };
};

module.exports = {
    quoteKey,
    unquoteKey,
    wrappedValue,
};
