import { createSelector } from 'reselect';
import * as S from '@webui-one/core/schema';
import { flatten } from '@webui-one/core/utils/array';
import { hasProp } from '@webui-one/core/utils/object';
import { isNonEmptyString } from '@webui-one/core/utils/string';
import * as KP from '@webui-one/core/keypath/kp-parser';
import { selectCaseValue, selectNode, selectNodes } from 'modules/nodes/selectors';
import { selectConfigForPath } from 'modules/data/selectors';
import { selectModule } from 'modules/devices/selectors';
import {
    selectSchema as selectSchema2,
    selectSchemaFormats,
} from 'modules/schemas2/selectors';
import { selectModelPrefix } from 'modules/systemSetting/reducer';
import { selectTypes } from 'modules/types/reducer';
import * as dataActionTypes from 'modules/data/actionTypes';
import * as actionTypes from './actionTypes';
import * as widgetKinds from './widgetKinds';
import * as widgetTypes from './widgetTypes';

export const loadStatus = {
    LOADING: 'LOADING',
    PARTIAL: 'PARTIAL',
    ERROR: 'ERROR',
    COMPLETE: 'COMPLETE',
    SHOULD_REEVALUATE: 'SHOULD_REEVALUATE',
};

const initialState = {
};

const getWidgetTypeByTypeName = (name) => {
    switch (name) {
    case 'date-and-time':
        return widgetTypes.LEAF_DATE_AND_TIME;
    case 'int8':
    case 'int16':
    case 'int32':
    case 'int64':
    case 'uint8':
    case 'uint16':
    case 'uint32':
    case 'uint64':
        return widgetTypes.LEAF_NUMBER;
    case 'boolean':
        return widgetTypes.LEAF_BOOLEAN;
    case 'empty':
        return widgetTypes.LEAF_EMPTY;
    case 'string':
        return widgetTypes.LEAF_STRING;
    case 'union':
        return widgetTypes.LEAF_UNION;
    case 'base64Binary':
        return widgetTypes.LEAF_BINARY;
    default:
        return widgetTypes.LEAF_NOT_SUPPORTED;
    }
};

export const getWidgetType = (schema, isInput = false) => {
    if (S.isLeafList(schema)) {
        const leafType = S.leafListType(schema);
        if (hasProp(leafType, 'enumeration')) {
            return widgetTypes.LEAF_ENUMERATION;
        }
        return getWidgetTypeByTypeName(leafType.name);
    }

    if (!S.isLeaf(schema) && !isInput) return undefined;

    if (S.isBits(schema)) return widgetTypes.LEAF_BITS;
    if (S.isEnumeration(schema)) return widgetTypes.LEAF_ENUMERATION;
    if (S.isLeafRef(schema)) return widgetTypes.LEAF_LEAFREF;
    if (S.isRange(schema)) return widgetTypes.LEAF_RANGE;
    if (S.isUnion(schema)) return widgetTypes.LEAF_UNION;
    if (S.isBinary(schema)) return widgetTypes.LEAF_BINARY;

    return getWidgetTypeByTypeName(S.type(schema).name);
};

export const getWidgetKind = (schema) => {
    switch (S.kind(schema)) {
    case 'action':
        return widgetKinds.KIND_ACTION;
    case 'case':
        return widgetKinds.KIND_CASE;
    case 'choice':
        return widgetKinds.KIND_CHOICE;
    case 'container':
        return widgetKinds.KIND_CONTAINER;
    case 'leaf-list':
        return widgetKinds.KIND_LEAFLIST;
    case 'list':
        return widgetKinds.KIND_LIST;
    case 'list-entry':
        return widgetKinds.KIND_LISTENTRY;
    case 'key':
        return widgetKinds.KIND_KEY;
    case 'leaf':
        return widgetKinds.KIND_LEAF;
    case 'module':
        return widgetKinds.KIND_MODULE;
    default:
        return widgetKinds.KIND_NOT_SUPPORTED;
    }
};

export const toSchemaPath = (path) => {
    if (!path) return undefined;
    let schemaPath = KP.pathWithoutKeys(path);

    if (path.endsWith('}')) {
        schemaPath += '{}';
    }
    return schemaPath;
};

const flattenChildren = (children) => {
    if (children && children.length > 0) {
        return children.map((c) => {
            const currentPath = toSchemaPath(S.keypath(c));
            const res = {
                path: currentPath,
                kind: S.kind(c),
                name: S.name(c),
                qname: S.qname(c),
                config: S.isConfig(c),
            };
            if (typeof S.isActionInput(c) !== 'undefined') {
                res.is_action_input = S.isActionInput(c);
            }
            if (typeof S.isActionOutput(c) !== 'undefined') {
                res.is_action_output = S.isActionOutput(c);
            }

            if (S.evaluatedWhenEntry(c) !== undefined) {
                res.evaluated_when_entry = S.evaluatedWhenEntry(c);
            }

            if (res.kind === 'leaf' && S.isEmptyLeaf(c)) {
                res.isEmptyLeaf = true;
            }

            if (res.kind === 'case') {
                res.hasEvaluatedWhenEntries = c.data.hasEvaluatedWhenEntries;
            }

            if (S.isMandatory(c)) {
                res.mandatory = true;
            }
            return res;
        });
    }
    return undefined;
};

const getLoadStatus = (schema, isRoot) => {
    if (isRoot) {
        return loadStatus.COMPLETE;
    }

    if (!S.isParent(schema)) {
        return loadStatus.COMPLETE;
    }

    if (S.isParent(schema) && schema.data.children !== undefined) {
        return loadStatus.COMPLETE;
    }

    return loadStatus.PARTIAL;
};

const getDataWithoutChildren = (schema) => {
    // eslint-disable-next-line no-unused-vars
    const { children: _children, cases: _cases, ...newData } = schema.data;
    return newData;
};

const getMetaWithoutTypes = (schema) => {
    // eslint-disable-next-line no-unused-vars
    const { types: _types, ...newMeta } = schema.meta;
    return newMeta;
};

const flattenSchema = (path, schema, isRoot, queryPath) => {
    const schemaPath = toSchemaPath(path);
    const querySchemaPath = toSchemaPath(queryPath);
    const isCase = S.isCase(schema);

    if ('children' in schema) {
        return schema;
    }

    if ('cases' in schema) {
        return schema;
    }

    const children = flattenChildren(S.children(schema), isCase);

    const cases = flattenChildren(S.cases(schema));

    const initial = {
        [schemaPath]: {
            data: getDataWithoutChildren(schema),
            meta: getMetaWithoutTypes(schema),
            loadStatus: getLoadStatus(schema, isRoot),
            children,
        },
    };

    if (querySchemaPath) {
        initial[querySchemaPath] = initial[schemaPath];
    }

    if (cases) {
        initial[schemaPath].cases = cases;

        return S.cases(schema).reduce((acc, cur) => {
            Object.assign(acc, flattenSchema(S.keypath(cur), cur, false));
            return acc;
        }, initial);
    }

    return S.children(schema).reduce((acc, cur) => {
        Object.assign(acc, flattenSchema(S.keypath(cur), cur, false));
        return acc;
    }, initial);
};


const handleGetSchemaStart = (state, action) => {
    const { reEvaluate, ...newState } = state;

    const path = toSchemaPath(action.path);
    if (path in state && !action.reload) {
        return newState;
    }

    newState[path] = {
        data: {},
        loadStatus: loadStatus.LOADING,
        meta: {},
    };

    return newState;
};

const handleGetSchemaError = (state, action) => ({
    ...state,
    [toSchemaPath(action.path)]: {
        data: {},
        error: action.error,
        loadStatus: loadStatus.ERROR,
        meta: {},
    },
});

const getSourcePath = (rootKeypath, schemaPath) => {
    const ancestor = KP.findCommonAncestor(rootKeypath, schemaPath);
    return schemaPath.replace(KP.pathWithoutKeys(ancestor), ancestor);
};

const getDependencies = (state, path) => (
    Object.keys(state).reduce((acc, curPath) => {
        const curSchema = state[curPath];

        if (!curSchema) return acc;
        if (!curSchema.data) return acc;

        if (S.kind(curSchema) === 'list') return acc;

        if (S.deps(curSchema)) {
            S.deps(curSchema).forEach((d) => {
                const src = getSourcePath(path, d);
                const dst = S.nodepath(curSchema) || curPath;

                if (!(src in acc)) {
                    acc[src] = { dependants: [dst] };
                } else {
                    acc[src] = {
                        ...acc[src],
                        dependants: [
                            ...acc[src].dependants || [], dst],
                    };
                }
            });
        }
        return acc;
    }, {})
);

const handleGetSchemaComplete = (state, action) => {
    const newState = {
        ...state,
        ...flattenSchema(action.path, action.schema, true, action.queryPath),
    };
    const dependencies = getDependencies(newState, action.path);
    if (Object.keys(dependencies).length > 0) {
        // eslint-disable-next-line no-underscore-dangle
        newState.__dependencies__ = dependencies;
    }
    return newState;
};

const handleReEvaluateSchema = (state, action) => {
    const schemaPath = toSchemaPath(action.path);
    return {
        ...state,
        reEvaluate: {
            [action.path]: [schemaPath, ...action.dependants],
        },
    };
};

const handleGetDataSchemaComplete = (state, action) => {
    const newState = {
        ...state,
        ...action.schemas,
    };
    const dependencies = getDependencies(newState, action.path);
    if (Object.keys(dependencies).length > 0) {
        // eslint-disable-next-line no-underscore-dangle
        newState.__dependencies__ = dependencies;
    }
    return newState;
};

export default function schemas(state = initialState, action) {
    switch (action.type) {
    case actionTypes.GET_SCHEMA_START:
        return handleGetSchemaStart(state, action);
    case actionTypes.GET_SCHEMA_COMPLETE:
        return handleGetSchemaComplete(state, action);
    case actionTypes.GET_SCHEMA_ERROR:
        return handleGetSchemaError(state, action);
    case actionTypes.REEVALUATE_SCHEMA:
        return handleReEvaluateSchema(state, action);
    case dataActionTypes.GET_DATA_COMPLETE:
        return handleGetDataSchemaComplete(state, action);
    case dataActionTypes.GET_DATA_ERROR:
        return handleGetSchemaError(state, action);
    default:
        return state;
    }
}

export const selectSchema = (state, path) => {
    const selectOptions = {
        selectSchemaFormat: selectSchemaFormats.SELECT_SCHEMA,
    };
    return selectSchema2(state, { path, selectOptions });
};

export const selectInfo = (state, path) => (
    S.info(selectSchema(state, path))
);

export const getInfoArray = (schema) => {
    if (!schema) {
        return [];
    }

    const elInfo = [];
    if (S.info(schema)) elInfo.push({ header: 'Information', content: S.info(schema) });
    if (S.minElements(schema) !== undefined) {
        elInfo.push(
            { header: 'Minimum number of elements', content: S.minElements(schema) },
        );
    }
    if (S.maxElements(schema) !== undefined) {
        elInfo.push(
            { header: 'Maximum number of elements', content: S.maxElements(schema) },
        );
    }
    if (S.units(schema)) elInfo.push({ header: 'Units', content: S.units(schema) });
    const unionInfo = S.unionInfoOptions(schema);
    if (unionInfo && unionInfo.length > 0) {
        if (S.unionIncludesEnumeration(schema)) {
            unionInfo.push('or select  from enumeration');
        }
        elInfo.push(
            { header: 'Valid values', content: unionInfo.reduce((acc, cur) => `${acc}. ${cur}`, '') },
        );
    }
    if (elInfo.length > 0) {
        elInfo.unshift({ header: 'Path', content: S.keypath(schema) });
        elInfo.unshift({ header: 'Name', content: S.name(schema) });
    }
    return elInfo;
};

export const selectInfoArray = (state, path) => {
    const schema = selectSchema(state, path);
    return getInfoArray(schema);
};

export const selectName = (state, path) => (
    S.name(selectSchema(state, path))
);

export function selectMetaType(schema, typeKey) {
    return schema.meta.types[typeKey];
}

export const schemasSelector = state => state.schemas;
export const typesSelector = state => state.types;

export const selectSchemaReselect = createSelector(
    (_, ownProps) => ownProps.path,
    schemasSelector,
    typesSelector,
    (path, stateSchemas, stateTypes) => {
        const schemaPath = toSchemaPath(path);
        if (!(schemaPath in stateSchemas)) {
            return undefined;
        }

        return selectSchema({ schemas: stateSchemas, types: stateTypes }, schemaPath);
    },
);

export const createSchemaSelector = () => selectSchemaReselect;

export const selectIsLoading = (state, path) => {
    const schema = selectSchema(state, path);
    return !!schema && (schema.loadStatus === loadStatus.LOADING);
};

const getKeypathWithoutChoiceAndCase = (parentPath, schema) => {
    const { nodepath } = schema.meta;
    const lastListEntryPath = parentPath.substring(0, parentPath.lastIndexOf('}') + 1);

    return nodepath.replace(KP.pathWithoutKeys(lastListEntryPath), lastListEntryPath);
};

const hasMetaNodepathWithoutKeys = schema => (
    schema.meta && schema.meta.nodepath && !schema.meta.nodepath.includes('}')
);

const selectItemWithType = (state, parentPath, schemaPath) => {
    const childSchema = selectSchema(state, schemaPath);

    const schema = {
        ...childSchema,
        meta: {
            ...childSchema.meta,
        },
        widgetKind: getWidgetKind(childSchema),
    };

    if (childSchema.meta && childSchema.meta.keypath && parentPath.includes('}')) {
        schema.meta.keypath = childSchema.meta.keypath.replace(
            childSchema.meta.parentKeypath,
            parentPath,
        );
    }

    if (hasMetaNodepathWithoutKeys(childSchema) && parentPath.includes('}')) {
        schema.meta.nodepath = getKeypathWithoutChoiceAndCase(parentPath, childSchema);
    }

    if (schema.widgetKind === widgetKinds.KIND_ACTION) {
        return schema;
    }
    schema.widgetType = getWidgetType(schema);
    return schema;
};

const isContainerLike = kind => (
    kind === widgetKinds.KIND_ACTION ||
    kind === widgetKinds.KIND_MODULE ||
    kind === widgetKinds.KIND_CONTAINER ||
    kind === widgetKinds.KIND_LISTENTRY
);

const selectCasesAsChildren = (state, cases) => (
    flatten(cases.map(c => selectSchema(state, c.path).children))
);

const isUndefined = v => v === undefined;

const shouldIncludeNode = (node, data, shouldShowEmpty, isEditMode) => {
    if (shouldShowEmpty && data === undefined) return true;
    if (!shouldShowEmpty && data === undefined) return false;
    if (shouldShowEmpty && node === undefined) return true;
    if (!shouldShowEmpty && node === undefined) return false;

    const { caseValue, isHidden, rows } = node;
    const { value, newValue, exists } = data;

    if (!shouldShowEmpty &&
        !exists &&
        isUndefined(rows) &&
        isUndefined(value) &&
        isUndefined(newValue) &&
        isUndefined(caseValue)) {
        return false;
    }

    if (!isEditMode && isHidden === true) return false;

    return true;
};

const filterOutHiddenNodes = (children, path, state, options) => {
    const { isEditMode, showEmpty } = options;
    return (
        children.filter((child) => {
            if (child.is_action_input === true || child.kind === 'action') {
                return true;
            }
            const nodePath = KP.schemaPathToKeypath(child.path, path);
            const node = selectNode(state, nodePath);
            const data = selectConfigForPath(state, nodePath);
            return shouldIncludeNode(node, data, showEmpty, isEditMode);
        })
    );
};

const filterChildrenByViewOptions = (options, children) => {
    let filteredChildren = children;
    if (!options.showOperdata) {
        filteredChildren = filteredChildren.filter(c => !(c.config === false));
    }
    return filteredChildren;
};

const getTypeSchema = (state, schema) => {
    const data = {};
    if (schema && schema.type) {
        data.type = schema.type;
    } else if (schema && schema.data && schema.data.type) {
        data.type = schema.data.type;
    }
    return {
        data,
        meta: {
            types: selectTypes(state || {}),
        },
    };
};

const selectLeafs = (state, schema, path, options) => {
    let children = (schema.children || []);
    if (!S.isAction(schema) && !S.isActionInput(schema)) {
        children = filterChildrenByViewOptions(options, children);
    }
    children = children
        .filter(c => (c.kind === 'key' || c.kind === 'leaf') && !S.isBits(getTypeSchema(state, c)))
        .filter(c => c.evaluated_when_entry !== false)
        .filter(c => c.is_action_output !== true);

    if (options.hideLeafs !== false) {
        children = filterOutHiddenNodes(children, path, state, options);
    }
    return children.reduce((acc, cur) => {
        Object.assign(acc, { [cur.path]: selectItemWithType(state, path, cur.path) });
        return acc;
    }, {});
};

const selectLeafLists = (state, schema, path, options) => {
    let children = schema.children || [];
    if (!S.isAction(schema) && !S.isActionInput(schema)) {
        children = filterChildrenByViewOptions(options, children);
    }
    children = children.filter(c => c.kind === 'leaf-list' || S.isBits(getTypeSchema(state, c)));
    children = children.filter(c => c.evaluated_when_entry !== false);
    children = children.filter(c => c.is_action_output !== true);
    children = filterOutHiddenNodes(children, path, state, options);
    return children.reduce((acc, cur) => {
        Object.assign(acc, { [cur.path]: selectItemWithType(state, path, cur.path) });
        return acc;
    }, {});
};

const selectActions = (state, schema, path) => {
    let children = schema.children || [];
    children = children.filter(c => c.kind === 'action');
    children = children.filter(c => c.evaluated_when_entry !== false);

    return children.reduce((acc, cur) => {
        Object.assign(acc, { [cur.path]: selectItemWithType(state, path, cur.path) });
        return acc;
    }, {});
};

export const toKeypath = (parentPath, childPath) => {
    if (KP.isListKeyPath(childPath)) return childPath;

    const parentSchemaPath = KP.pathWithoutKeys(parentPath);
    return childPath.replace(parentSchemaPath, parentPath);
};

const getSubSchemas = (state, path, children, options, filter) => (
    children.filter(filter).reduce((acc, cur) => {
        const newOptions = {
            ...options,
            levels: options.levels - 1,
        };
        const childKeypath = toKeypath(path, cur.path);
        // eslint-disable-next-line no-use-before-define
        const subSchema = selectGroupedSchemas(state, childKeypath, newOptions);
        if (subSchema) Object.assign(acc, subSchema);
        return acc;
    }, {})
);

const filterChildrenByOptions = (children, options) => {
    if (children === undefined) return [];

    return children.reduce((acc, cur) => {
        const kind = cur.kind;
        if (cur.evaluated_when_entry === false && options.hideNotEvaluatedWhenEntries) return acc;
        if (kind === 'container' && options.hideContainers) return acc;
        if (kind === 'list' && options.hideLists) return acc;
        acc.push(cur);
        return acc;
    }, []);
};

const createCaseFilter = value => (c) => {
    if (value && value.indexOf(':') > -1) {
        return c.qname === value;
    }
    return c.name === value;
};

const selectGroupedChoiceSchemas = (state, schema, path, options) => {
    const schemaPath = toSchemaPath(path);
    const nodePath = KP.schemaPathToKeypath(schemaPath, path);
    const node = selectNodes(state)[nodePath];
    if (node) {
        const { isEditMode, showEmpty } = options;
        if (!showEmpty && node.caseValue === undefined) return false;
        if (!isEditMode && node && node.isHidden === true) {
            return false;
        }
    }
    const item = {
        schemaPath,
        data: schema.data,
        meta: schema.meta,
        widgetKind: widgetKinds.KIND_CHOICE,
        loadStatus: schema.loadStatus,
    };

    if (schema.kindpath) item.kindpath = schema.kindpath;

    item.cases = schema.cases;
    const caseValue = selectCaseValue(state, path);
    if (caseValue) {
        // Choices should not filter containers to prevent empty selects
        const newOptions = { ...options, hideContainers: false };
        const cases = schema.cases.filter(createCaseFilter(caseValue));

        const children = selectCasesAsChildren(state, cases);

        item.leafs = selectLeafs(state, { children }, path, newOptions);
        item.leafLists = selectLeafLists(state, { children }, path, newOptions);

        return {
            [schemaPath]: item,
        };
    }

    return {
        [schemaPath]: item,
    };
};

const shouldHide = (kind, children, options) => {
    if (options.hideEmptyContainers) {
        if (children.length === 0) return true;
        if (!options.showActions && kind !== widgetKinds.KIND_ACTION) {
            if (children.filter(c => c.kind !== 'action').length === 0) return true;
        }
    }
    return false;
};

const getPrefix = item => (item.qname || `:${item.name}`).split(':')[0];

const filterDeviceConfigChildren = (children, prefixes, options) => {
    if (children === undefined || options.hideDeviceConfig) return [];
    return children.reduce((acc, cur) => {
        if (!prefixes || prefixes.length === 0) return acc;

        const prefix = getPrefix(cur);
        if (prefix && !prefixes.includes(prefix)) return acc;

        acc.push(cur);
        return acc;
    }, []);
};

const isDeviceConfig = path => /^\/ncs:devices\/device\{[^}]+\}\/config.*/.test(path);

const selectDevicePrefixes = (state, path) => {
    const module = selectModule(state, path);
    if (!module) return undefined;
    if (!module.modules) return undefined;
    return module.modules
        .map(deviceModuleNames => selectModelPrefix(state, deviceModuleNames))
        .filter(prefix => isNonEmptyString(prefix));
};

const selectGroupedContainerLikeSchemas = (state, schema, path, options) => {
    const schemaPath = toSchemaPath(path);
    const kind = getWidgetKind(schema);

    const item = {
        schemaPath,
        data: schema.data,
        meta: schema.meta,
        widgetKind: kind,
        widgetType: getWidgetType(schema),
        loadStatus: schema.loadStatus,
    };
    if (schema.kindpath) item.kindpath = schema.kindpath;
    let children = filterChildrenByOptions(schema.children, options);

    if (schema.caseAncestors) {
        item.caseAncestors = schema.caseAncestors;
    }

    if (isDeviceConfig(path)) {
        const prefixes = selectDevicePrefixes(state, path);
        children = filterDeviceConfigChildren(schema.children, prefixes, options);
    }

    if (shouldHide(kind, children, options)) return {};

    const subContainers = getSubSchemas(state, path, children, options, c => c.kind === 'container');
    const subLists = getSubSchemas(state, path, children, options, c => c.kind === 'list');
    const subChoices = getSubSchemas(state, path, children, options, c => c.kind === 'choice');

    item.includesActions = Object.keys(selectActions(state, schema, path)).length > 0;
    item.includesOper = S.isOper(schema);

    if (options.showActions) {
        item.actions = selectActions(state, schema, path);
    }
    if (!(options.hideDeviceConfig && isDeviceConfig(path))) {
        item.leafs = selectLeafs(state, schema, path, options);
        item.leafLists = selectLeafLists(state, schema, path, options);
    }

    return {
        [schemaPath]: item,
        ...subChoices,
        ...subLists,
        ...subContainers,
    };
};


const selectEmptySchema = (schema, path) => {
    const schemaPath = toSchemaPath(path);
    const kind = getWidgetKind(schema);

    const item = {
        schemaPath,
        data: schema.data,
        meta: schema.meta,
        widgetKind: kind,
        widgetType: getWidgetType(schema),
        loadStatus: schema.loadStatus,
    };
    if (schema.kindpath) item.kindpath = schema.kindpath;

    item.includesOper = S.isOper(schema);
    item.includesActions = S.isAction(schema);

    return {
        [schemaPath]: {
            ...item,
            actions: [],
            leafs: [],
            leafLists: [],
        },
    };
};

const selectChildrenDataSchemas = (state, children) => (
    (children || [])
        .reduce((acc, cur) => {
            acc.push(selectSchema(state, cur.path));
            return acc;
        }, [])
);

export const selectGroupedListSchemas = (state, schema, path) => {
    const schemaPath = toSchemaPath(path);
    const kind = getWidgetKind(schema);

    if (S.evaluatedWhenEntry(schema) !== false) {
        const item = {
            includesOper: S.isOper(schema),
            data: {
                ...schema.data,
                children: selectChildrenDataSchemas(state, schema.children),
            },
            meta: schema.meta,
            widgetKind: kind,
            widgetType: getWidgetType(schema),
            loadStatus: schema.loadStatus,
        };
        if (schema.kindpath) item.kindpath = schema.kindpath;
        return {
            [schemaPath]: item,
        };
    }
    return {};
};

const isEmptyDeviceConfig = (state, path) => {
    const module = selectModule(state, path);

    if (!module) return true;
    if (module.isLoading === true) return true;
    if (module.modules && module.modules.length === 0) return true;
    return false;
};

export const selectGroupedSchemas = (state, path, options) => {
    const schema = selectSchema(state, path);
    const schemaPath = toSchemaPath(path);
    const isAction = S.isAction(schema);
    const isActionInput = S.isActionInput(schema);
    if (!schema) return {};

    if (schema.loadStatus === loadStatus.ERROR) return {};

    if (!isAction && !isActionInput) {
        if (!options.showOperdata && !S.isConfig(schema)) {
            return {
                [schemaPath]: {
                    widgetKind: widgetKinds.KIND_NONE,
                    includesOper: true,
                },
            };
        }
    }

    const kind = getWidgetKind(schema);

    if (S.isActionOutput(schema)) return {};

    if (options.hideNotLoadedModuleDeviceConfig && isDeviceConfig(path)) {
        if (isEmptyDeviceConfig(state, path, schema)) {
            return selectEmptySchema(schema, path, schemaPath);
        }
    }

    if (kind === widgetKinds.KIND_CHOICE) {
        // Choices should not be filtered by levels to prevent empty selects
        const newOptions = {
            ...options,
            levels: -1,
            showEmpty: true,
        };
        return selectGroupedChoiceSchemas(state, schema, path, newOptions);
    }

    if (kind === widgetKinds.KIND_CASE) {
        const newOptions = {
            ...options,
            levels: -1,
            showEmpty: true,
        };
        return selectGroupedContainerLikeSchemas(state, schema, path, newOptions);
    }

    if (kind === widgetKinds.KIND_LIST) {
        return selectGroupedListSchemas(state, schema, path, options);
    }

    if (options.levels === 0) {
        return selectEmptySchema(schema, path, schemaPath);
    }

    if (isContainerLike(kind)) {
        return selectGroupedContainerLikeSchemas(state, schema, path, options);
    }

    return {
        [schemaPath]: {
            schemaPath,
            data: schema.data,
            includesActions: S.isAction(schema),
            includesOper: S.isOper(schema),
            meta: schema.meta,
            widgetKind: kind,
            widgetType: getWidgetType(schema),
            loadStatus: schema.loadStatus,
            kindpath: schema.kindpath,
        },
    };
};

export const selectChildrenForInput = (state, schema, filterFunc) => {
    if (schema && schema.children) {
        const children = schema.children.filter(filterFunc).map((child) => {
            const sch = selectSchema(state, child.path);
            sch.path = child.path;
            sch.widgetType = getWidgetType(sch, true);
            return sch;
        });
        return children;
    }
    return [];
};


export const selectShouldReEvaluate = (state, path, paths) => {
    if (!paths || paths.length === 0) return false;
    if (!state.schemas) return false;
    if (!state.schemas.reEvaluate) return false;

    const reEvaluate = state.schemas.reEvaluate;

    const reEvalKeys = Object.keys(reEvaluate);
    if (reEvalKeys.length === 0) return false;

    const reEvalPath = reEvalKeys.find(p => p.startsWith(path));

    if (!reEvalPath) return false;

    const reEvalPaths = reEvaluate[reEvalPath];
    if (!reEvalPaths || reEvalPaths.length === 0) return false;

    const paths2 = new Set(reEvalPaths);

    const intersection = new Set([...paths].filter(p => paths2.has(p)));

    return intersection.size > 0;
};

export const isSchemaLoaded = (schema) => {
    if (!schema) return false;
    if (schema.loadStatus !== loadStatus.COMPLETE) return false;

    return true;
};

export const selectCurrentSchema = (
    state,
    {
        path,
        funcs: {
            selectGroupedSchemasFunc = selectGroupedSchemas,
        } = {},
    } = {},
) => {
    const schemaFilter = {
        hideEmptyContainer: true,
        hideNotLoadedModuleDeviceConfig: true,
    };
    const groupedSchemas = selectGroupedSchemasFunc(state, path, schemaFilter);
    return groupedSchemas && groupedSchemas[toSchemaPath(path)];
};

export const selectSchemasByPaths = (state, paths) => (
    paths.reduce((acc, cur) => {
        acc[cur] = selectSchema(state, cur);
        return acc;
    }, {})
);

export const selectIsOperdata = (state, path) => (
    S.isOper(selectSchema(state, path))
);

