import {
    quoteKey,
} from '@webui-one/core/functional_utils';
import { rstrip } from '@webui-one/core/utils/string';
import { isPositiveNumber } from '@webui-one/core/utils/number';

const emptyObject = Object.freeze({});

export const selectData = state => state.data;

export const selectConfig = state => state && state.data && state.data.config;

export const selectIsConfigLoading = state => (
    state &&
    state.data &&
    state.data.config &&
    state.data.config.isLoading
);

export const selectConfigForPath = (state, path) => (
    (selectConfig(state) || {})[path]
);

export const selectConfigIsLoading = (state, path) => (
    (selectConfigForPath(state, path) || {}).isLoading
);

export const getRowKeypath = (basepath, keynames = [], values, ix) => {
    const identifiers = keynames.reduce((acc, cur) => {
        const value = values[cur];
        if (typeof value === 'string') {
            acc.push(quoteKey(value));
        } else if (Number.isFinite(value)) {
            acc.push(`${value}`);
        }
        return acc;
    }, []);

    if (identifiers.length === 0) return `${basepath}{${ix + 1}}`;

    return `${basepath}{${identifiers.join(' ')}}`;
};

export const selectExists = (state, path) => (
    state.data && state.data.config && path in state.data.config
);

export const selectErrorState = state => (
    state.data && state.data.error ? state.data.error : undefined
);

export const selectHasError = (state, path) => (
    path in (selectErrorState(state) || emptyObject)
);

export const selectTotalNumberOfResults = (state, path) => (
    (state.data.config &&
     state.data.config[path] &&
     Number.isFinite(state.data.config[path].totalNumberOfResults))
        ? state.data.config[path].totalNumberOfResults
        : 0
);

export const selectPathIsNotEmpty = (data, path) => {
    const pathWithSlash = `${rstrip(path, '/')}/`;
    const notEmpty = Object.keys(data).some((p) => {
        const pData = data[p];
        if ((p === path || p.startsWith(pathWithSlash)) &&
            (pData.value !== undefined ||
             pData.newValue !== undefined ||
             isPositiveNumber(pData.totalNumberOfResults))) {
            return true;
        }
        return false;
    });
    return notEmpty;
};
