import { connect } from 'react-redux';
import { login, loginClearErrorAction } from 'modules/login/actions';
import { selectError } from 'modules/login/reducer';
import Login from './Login';


const mapDispatchToProps = dispatch => ({
    onLogin(user, passwd) {
        dispatch(login(user, passwd));
    },
    clearError() {
        dispatch(loginClearErrorAction());
    },
});

const mapStateToProps = state => (
    {
        error: selectError(state),
    }
);

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(Login);

export {
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
