/* eslint-disable import/prefer-default-export */

export const ensureNumber = num => (
    Number.isFinite(num) ? num : 0
);

export const isNumber = value => Number.isFinite(value);
export const isPositiveNumber = value => isNumber(value) && value > 0;

export const toNumber = n => ~~n; // eslint-disable-line no-bitwise
