import * as actionTypes from './actionTypes';

export const applicationNameSetAction = name => ({
    type: actionTypes.APPLICATION_NAME_SET,
    name,
});
export const setAuthenticated = authenticated => ({
    type: actionTypes.AUTHENTICATED_SET,
    authenticated,
});
export const setShowShortcutPanel = value => ({ type: actionTypes.SHOW_SHORTCUT_PANEL, value });
export const showHelpAction = value => ({ type: actionTypes.SHOW_HELP, value });
