import { applicationsSelector } from 'modules/applications/selectors';
import { getPathname } from '@webui-one/core/utils/browser';
import { getApplicationsAsyncAction } from 'modules/applications/actions';
import { isString } from '@webui-one/core/utils/string';

const dummyApplication = Object.freeze({ title: '' });

export const isApplicationPath = (href, applicationPath) => (
    applicationPath.startsWith(href) &&
    applicationPath !== '' && (
        applicationPath === href ||
        applicationPath[href.length] === '/' ||
        applicationPath[href.length] === '?'
    )
);

export const findApplicationByPath = (state, applicationPath) => {
    const applications = applicationsSelector(state);
    return Object.keys(applications).reduce((app, key) => {
        const application = applications[key];
        return isApplicationPath(application.href, applicationPath)
            ? application
            : app;
    }, dummyApplication);
};

export const findCurrentApplication = state => (
    findApplicationByPath(state, getPathname())
);

export const getApplicationName = (state, ownProps) => {
    if (ownProps && isString(ownProps.applicationName)) {
        return ownProps.applicationName;
    }
    return findCurrentApplication(state).title;
};

export const initApplication = (store) => {
    store.dispatch(getApplicationsAsyncAction());
};

export const getApplicationStorageTag = () => 'meta';
