import * as S from '@webui-one/core/schema';

import { defaultTo } from '@webui-one/core/utils/functional';
import { compact } from '@webui-one/core/utils/array';
import { assignDeep } from '@webui-one/core/utils/object';

import {
    loadStatus,
} from 'modules/schemas/reducer';

import {
    parsePath,
} from './utils';

export const selectSchemaFormats = {
    GET_SCHEMA: 'GET_SCHEMA',
    SELECT_SCHEMA: 'SELECT_SCHEMA',
    CASE_AS_OBJECT: 'CASE_AS_OBJECT',
    CHILD_AS_OBJECT: 'CHILD_AS_OBJECT',
    DATA_CHILD: 'DATA_CHILD',
};

const emptyObject = Object.freeze({});

export const selectSchemas = state => state.schemas2 || emptyObject;
export const selectLoadingSchemas = state => selectSchemas(state).loading || emptyObject;

export const selectStaticSchemas = state => selectSchemas(state).staticSchemas || emptyObject;
export const selectEvaluatedSchemas = state => selectSchemas(state).evaluatedSchemas || emptyObject;

const getMetaTypesSchema = (stateTypes, type) => {
    if (!type) return undefined;
    if (type.primitive === true) return undefined;
    const typeName = `${type.namespace}:${type.name}`;

    const typeEntry = stateTypes[typeName];

    return {
        meta: {
            types: {
                [typeName]: typeEntry,
            },
        },
    };
};

const accesses = {
    c: 'create',
    r: 'read',
    u: 'update',
    d: 'delete',
    e: 'execute',
};

const createAccessFromName = (name) => {
    const chars = [...name];
    const access = { create: false, read: false, update: false, delete: false, execute: false };
    chars.forEach(c => (access[accesses[c]] = true)); // eslint-disable-line no-return-assign
    return access;
};

const getAccessSchema = (schema) => {
    if (!schema || !schema.data) return undefined;

    const { access: accessRuleName } = schema.data;

    if (!accessRuleName) return undefined;

    const access = createAccessFromName(accessRuleName);
    return {
        data: {
            access,
        },
    };
};

const statusTypes = {
    ERROR: 'ERROR',
    NEW_OBJECT: 'NEW_OBJECT',
    NOT_EVALUATED: 'NOT_EVALUATED',
    NOT_FOUND: 'NOT_FOUND',
    ROOT_NOT_FOUND: 'ROOT_NOT_FOUND',
};

const createGetDataChildrenReducer = (
    state,
    parentPath,
    {
        selectOptions: parentOptions = {},
        funcs: {
            // eslint-disable-next-line no-use-before-define
            selectSchemaFunc = selectSchema,
        } = {},
    } = {},
) => (acc, childName) => {
    const path = `${parentPath}/${childName}`;

    const schema = selectSchemaFunc(
        state,
        {
            path,
            selectOptions: {
                ...parentOptions,
                levels: parentOptions.levels - 1,
                selectSchemaChildFormat: selectSchemaFormats.DATA_CHILD,
            },
        },
    );

    if (!schema) return acc; // evaluated child

    if (parentOptions.notWrappedDataChildren) {
        acc.push(schema.data);
    } else {
        acc.push(schema);
    }

    return acc;
};

const getDataChildren = (
    state,
    parent,
    children,
    {
        selectOptions = {},
        funcs: {
            createGetDataChildrenReducerFunc = createGetDataChildrenReducer,
        } = {},
    } = {},
) => {
    if (children === undefined) return undefined;
    const parentPath = parent.data.kind === 'list' ? `${parent.path}{}` : parent.path;
    return children.reduce(
        createGetDataChildrenReducerFunc(state, parentPath, { selectOptions }),
        [],
    );
};

const deviceConfigRE = new RegExp('/ncs:devices/device{([^]+)}/(config/([^:]+):.*)');

const statusError = reason => ({ status: statusTypes.ERROR, reason });
const statusNotFound = reason => ({ status: statusTypes.NOT_FOUND, reason });
const statusRootNotFound = reason => ({ status: statusTypes.ROOT_NOT_FOUND, reason });
const statusNotEvaluated = reason => ({ status: statusTypes.NOT_EVALUATED, reason });

const statusPathUndefined = () => statusError('props.path undefined');
const statusSchemas2Undefined = () => statusNotFound('state.schemas2 undefined');
const statusStaticSchemasUndefined = () => (
    statusNotFound('state.schemas2.staticSchemas undefined')
);
const statusEvaluatedSchemasUndefined = () => (
    statusNotEvaluated('state.schemas2.evaluatedSchemas undefined')
);
const statusRootSchemaUndefined = path => statusRootNotFound(`No root schema for path: ${path}`);
const statusStateTypesUndefined = () => statusError('state.types undefined');

const verifyState = state => () => {
    const { schemas2 } = state;
    if (!schemas2) return statusSchemas2Undefined();

    return {};
};

const verifyProps = (_, ownProps) => () => {
    const { path } = ownProps;
    if (!path) return statusPathUndefined();
    return {};
};

const parsePathByType = (path, isStatic) => {
    const { rootPath, rootSchemaPath, subPath } = parsePath(path);
    if (isStatic) return { rootPath: rootSchemaPath, subPath };
    return { rootPath, subPath };
};

const resolveSchema = ({ stateSchemas, isStatic }) => {
    const resolveSubSchema = ({ schemas, subPath, followSymlink, followNodepath }) => {
        const subSchema = schemas[subPath];
        if (subSchema === undefined) return undefined;

        const { symlink, nodepath, ...symlinkSubSchema } = subSchema;

        if (followSymlink && symlink) {
            return assignDeep(
                {},
                { nodepath },
                symlinkSubSchema,
                // eslint-disable-next-line no-use-before-define
                resolve({ path: symlink, followNodepath: false }),
            );
        }
        if (followNodepath && nodepath) {
            return assignDeep(
                {},
                { nodepath },
                symlinkSubSchema,
                // eslint-disable-next-line no-use-before-define
                resolve({ path: nodepath, followSymlink: false }),
            );
        }

        return subSchema;
    };

    const resolve = ({ path, followSymlink = true, followNodepath = true }) => {
        const { rootPath, subPath } = parsePathByType(path, isStatic);
        const root = stateSchemas[rootPath];

        if (root === undefined) {
            return statusRootSchemaUndefined(rootPath);
        }

        const { schemas } = root;

        let symlinkSubSchema;

        const rootSchema = schemas['.'];

        if (rootSchema) {
            const { symlink: rootSymlink } = rootSchema;

            if (followSymlink && rootSymlink) {
                const symlinkRoot = stateSchemas[rootSymlink];
                symlinkSubSchema = resolveSubSchema({
                    schemas: symlinkRoot.schemas,
                    subPath,
                    followSymlink,
                    followNodepath,
                });
            }
        }

        const subSchema = resolveSubSchema({
            schemas,
            subPath,
            followSymlink,
            followNodepath,
        });

        if (subSchema === undefined && symlinkSubSchema === undefined) {
            return undefined;
        }

        return assignDeep({}, symlinkSubSchema, subSchema);
    };
    return resolve;
};


const appendStaticSchema = (state, ownProps) => () => {
    const { schemas2: { staticSchemas } } = state;

    if (!staticSchemas) return statusStaticSchemasUndefined();

    const match = deviceConfigRE.exec(ownProps.path);

    const path = match !== null ? `/ned-id:${match[3]}/${match[2]}` : ownProps.path;
    const schema = resolveSchema({ stateSchemas: staticSchemas, isStatic: true })({ path });
    return schema;
};

const appendMetaTypesSchema = state => (schema) => {
    if (!state.types) return statusStateTypesUndefined();
    return defaultTo({ meta: {} }, getMetaTypesSchema(state.types, S.type(schema)));
};

const appendAccessRules = () => schema => (
    defaultTo({}, getAccessSchema(schema))
);

const appendDataChildren = (state, props = {}) => (schema) => {
    const {
        selectOptions = {},
        funcs: { getDataChildrenFunc = getDataChildren } = {},
    } = props;

    if (S.isParent(schema) === false) return {};

    const { children } = schema;
    // if (children === undefined && levels === 0) return {};

    if (children === undefined) return { loadStatus: loadStatus.PARTIAL };
    if (children.length === 0) return { data: { children: [] } };

    const dataChildren = getDataChildrenFunc(state, schema, children, { selectOptions });

    if (dataChildren.length === 0) return { loadStatus: loadStatus.PARTIAL };

    return {
        data: {
            children: dataChildren,
        },
    };
};

const appendCases = (state, props = {}) => (schema) => {
    const { cases } = schema;

    if (!cases || cases.length === 0) return {};

    const {
        selectOptions: parentOptions = {},
        funcs: { getDataChildrenFunc = getDataChildren } = {},
    } = props;

    const selectOptions = {
        ...parentOptions,
        // levels: parentOptions.levels + 2,
    };

    const dataCases = getDataChildrenFunc(state, schema, cases, { selectOptions });

    return {
        data: {
            cases: dataCases,
        },
    };
};

const appendChildrenAsObjects = (state, { path, selectOptions = {} }) => (schema) => {
    const {
        levels,
    } = selectOptions;

    const { children, ...schemaWithoutChildren } = schema;

    if (levels === 0) return schemaWithoutChildren;

    if (children && children.length > 0) {
        const parent = {
            ...schema,
            path,
        };
        // eslint-disable-next-line no-use-before-define
        const newChildren = compact(children.map(createChildAsObjectMapper(state, parent)));
        if (newChildren.length !== children.length) {
            return { ...schema, children: undefined };
        }
        return { ...schema, children: newChildren };
    }

    return schema;
};

const appendCasesAsObjects = state => (schema) => {
    const { cases } = schema;

    if (cases && cases.length > 0) {
        // eslint-disable-next-line no-use-before-define
        const newCases = cases.map(createCaseAsObjectMapper(state, schema));
        return { ...schema, cases: newCases };
    }
    return schema;
};

const shouldHideSchema = (schema, hideEvaluatedWhenFalseEntries) => {
    if (hideEvaluatedWhenFalseEntries === false) return false;
    if (schema.data.evaluated_when_entry === false) return true;
    return false;
};

const createAppendEvaluatedSchema = ({
    hideEvaluatedWhenFalseEntries = true,
} = {}) => (state, ownProps) => () => {
    const { schemas2: { evaluatedSchemas } } = state;
    if (!evaluatedSchemas) return statusEvaluatedSchemasUndefined();

    const { path } = ownProps;

    const evaluatedSchema = resolveSchema({
        stateSchemas: evaluatedSchemas,
        isStatic: false,
    })({ path });

    if (evaluatedSchema === undefined) {
        return { path, loadStatus: loadStatus.SHOULD_REEVALUATE };
    }

    if (evaluatedSchema.status === statusTypes.NOT_EVALUATED) {
        return { path, loadStatus: loadStatus.SHOULD_REEVALUATE };
    }

    if (evaluatedSchema.status === statusTypes.ROOT_NOT_FOUND) {
        return { path, loadStatus: loadStatus.SHOULD_REEVALUATE };
    }

    if (evaluatedSchema.error) {
        return { path, loadStatus: loadStatus.ERROR, error: evaluatedSchema.error };
    }

    if (shouldHideSchema(evaluatedSchema, hideEvaluatedWhenFalseEntries)) {
        return { path, status: statusNotEvaluated('hideEvaluatedWhenFalseEntries is true') };
    }

    return {
        ...evaluatedSchema,
        path,
    };
};

const appendMeta = (_, { path }) => (schema) => {
    const {
        ancestorRelativeKeypath,
        choicePath,
        nodepath,
        ns,
        relativeName,
        types,
    } = schema;

    const meta = {
        keypath: path,
        prefix: ns,
    };

    if (types) meta.types = types;

    if (ancestorRelativeKeypath) {
        const { rootPath } = parsePath(path);
        if (ancestorRelativeKeypath === '.') {
            meta.nodepath = `${rootPath}/${relativeName}`;
        } else {
            meta.nodepath = `${rootPath}/${ancestorRelativeKeypath}/${relativeName}`;
        }
    }

    if (nodepath) meta.nodepath = nodepath;

    if (choicePath) meta.choicePath = choicePath;

    return {
        ...schema,
        meta,
    };
};

const rewriteCaseAncestors = (_, { path }) => (schema) => {
    const {
        caseAncestors: oldCaseAncestors,
    } = schema;
    const { rootPath } = parsePath(path);

    if (oldCaseAncestors) {
        const caseAncestors = oldCaseAncestors.reduce((acc, cur) => {
            const caseAncestorPath = `${rootPath}/${cur}`;
            if (acc.includes(caseAncestorPath)) return acc;
            acc.push(caseAncestorPath);
            return acc;
        }, []);

        return {
            ...schema,
            caseAncestors,
        };
    }
    return schema;
};

const combineCreators = (...creators) => (state, ownProps) => (
    creators.reduce((acc, currentCreator) => {
        if (acc === undefined) return acc;
        if (acc.status === statusTypes.ERROR) return acc;
        if (acc.status === statusTypes.NOT_FOUND) return acc;
        if (acc.status === statusTypes.ROOT_NOT_FOUND) return acc;

        const res = currentCreator(state, ownProps)(acc);

        if (res === undefined) return res;

        if (res.status === statusTypes.NEW_OBJECT) {
            const { status, ...newObject } = res;
            return newObject;
        }

        return assignDeep(acc, res);
    }, {})
);

const createDeleteAttributes = attributes => () => (schema) => {
    // eslint-disable-next-line no-param-reassign
    attributes.forEach(attribute => delete schema[attribute]);
    return schema;
};

/* eslint-disable no-console, no-unused-vars */
const createLogSchema = attributes => () => (schema) => {
    if (attributes) {
        [attributes].flat().forEach(a => console.log(`LOG schema.${a}=${schema[a]}`));
        return schema;
    }
    console.log('LOG schema=', schema);
    return schema;
};
/* eslint-enable no-console, no-unused-vars */

const setLoadStatus = () => (schema) => {
    const { children, error, loadStatus: lStatus } = schema;

    if (lStatus === loadStatus.LOADING) return schema;

    if (error) {
        return {
            ...schema,
            loadStatus: loadStatus.ERROR,
        };
    }
    if (S.isParent(schema) && children === undefined) {
        return {
            ...schema,
            loadStatus: loadStatus.PARTIAL,
        };
    }

    if (schema.status === statusTypes.NOT_EVALUATED ||
        schema.loadStatus === loadStatus.SHOULD_REEVALUATE) {
        return {
            ...schema,
            loadStatus: loadStatus.SHOULD_REEVALUATE,
        };
    }
    return {
        ...schema,
        loadStatus: loadStatus.COMPLETE,
    };
};

const selectSchemaInSelectSchema1Format = combineCreators(
    verifyState,
    verifyProps,
    appendStaticSchema,
    createAppendEvaluatedSchema(),
    appendMetaTypesSchema,
    appendAccessRules,
    appendChildrenAsObjects,
    appendCasesAsObjects,
    setLoadStatus,
    rewriteCaseAncestors,
    appendMeta,
    createDeleteAttributes([
        'relativeName',
        'widgetKind',
        'ns',
        'path',
        'symlink',
    ]),
);

const createChildObject = (_, { path }) => (schema) => {
    const {
        hasEvaluatedWhenEntries,
        data,
    } = schema;

    const {
        config,
        evaluated_when_entry: evaluatedWhenEntry,
        is_action_input: isActionInput,
        is_action_output: isActionOutput,
        kind,
        mandatory,
        name,
        qname,
        type,
    } = data;

    const childObject = {
        status: statusTypes.NEW_OBJECT,
        path,
        kind,
        name,
        qname,
        type,
    };

    childObject.config = config !== false;

    if (mandatory) childObject.mandatory = true;
    if (evaluatedWhenEntry === false) childObject.evaluated_when_entry = false;
    if (isActionInput) childObject.is_action_input = true;
    if (isActionOutput) childObject.is_action_output = true;
    if (S.isEmptyLeaf(schema)) childObject.isEmptyLeaf = true;

    if (kind === 'case') childObject.hasEvaluatedWhenEntries = hasEvaluatedWhenEntries;

    return childObject;
};

const createCaseAsObjectMapper = (state, parent) => (childName) => {
    const path = `${parent.path}/${childName}`;
    // eslint-disable-next-line no-use-before-define
    const schema = selectSchemaInChildObjectFormat(state, { path });
    if (schema === undefined) throw new Error(`No case schema found for path: ${path}`);
    return schema;
};

const createChildAsObjectMapper = (state, parent) => (childName) => {
    const parentPath = S.isList(parent) ? `${parent.path}{}` : parent.path;

    const path = `${parentPath}/${childName}`;
    // eslint-disable-next-line no-use-before-define
    const schema = selectSchemaInChildObjectFormat(state, { path });
    if (schema === undefined) return undefined;
    return schema;
};

const selectSchemaInChildObjectFormat = combineCreators(
    appendStaticSchema,
    createAppendEvaluatedSchema({ hideEvaluatedWhenFalseEntries: false }),
    setLoadStatus,
    createChildObject,
);

const createOptions = ({ selectOptions = {} }) => {
    const { selectSchemaFormat } = selectOptions;
    const options = { ...selectOptions };

    switch (selectSchemaFormat) {
    case selectSchemaFormats.GET_SCHEMA:
        options.notWrappedDataChildren = true;
        break;
    default:
        break;
    }

    if (options.levels === undefined) options.levels = 2;

    return options;
};

const isFound = (schema) => {
    if (!schema) return false;
    if (schema.status === statusTypes.NOT_FOUND) return false;
    if (schema.status === statusTypes.ROOT_NOT_FOUND) return false;
    return true;
};

const internalSelectSchema = (
    {
        selectorFunc = selectSchemaInSelectSchema1Format,
        createOptionsFunc = createOptions,
    } = {},
    state,
    ownProps = {},
) => {
    const selectOptions = createOptionsFunc(ownProps);

    const schema = selectorFunc(state, { ...ownProps, selectOptions });

    if (isFound(schema)) return schema;

    return undefined;
};

export const selectSchema = (state, ownProps) => internalSelectSchema(undefined, state, ownProps);

export const selectIsLoading = (state, path) => {
    const schema = selectSchema(state, path);
    return !!schema && (schema.loadStatus === loadStatus.LOADING);
};

export const selectSchemaIsLoading = (state, path) => {
    const loading = selectLoadingSchemas(state);
    const loadingStatus = loading[path];
    if (!!loadingStatus && loadingStatus.loadStatus === loadStatus.LOADING) {
        return true;
    }
    return false;
};

export const selectIsLoaded = (state, path) => {
    const schema = selectSchema(state, path);

    if (schema && S.isParent(schema) && schema.children === undefined) {
        schema.loadStatus = loadStatus.PARTIAL;
        return false;
    }

    if (schema) return true;
    return false;
};

export {
    appendCases as _appendCases,
    appendDataChildren as _appendDataChildren,
    createAppendEvaluatedSchema as _createAppendEvaluatedSchema,
    appendStaticSchema as _appendStaticSchema,
    combineCreators as _combineCreators,
    createOptions as _createOptions,
    getDataChildren as _getDataChildren,
    getMetaTypesSchema as _getMetaTypesSchema,
    internalSelectSchema as _internalSelectSchema,
    resolveSchema as _resolveSchema,
    selectSchemaInChildObjectFormat as _selectSchemaInChildObjectFormat,
    selectSchemaInSelectSchema1Format as _selectSchemaInSelectSchema1Format,
    statusEvaluatedSchemasUndefined as _statusEvaluatedSchemasUndefined,
    statusPathUndefined as _statusPathUndefined,
    statusRootSchemaUndefined as _statusRootSchemaUndefined,
    statusSchemas2Undefined as _statusSchemas2Undefined,
    statusStaticSchemasUndefined as _statusStaticSchemasUndefined,
    statusTypes as _statusTypes,
    verifyState as _verifyState,
};
