import * as actionTypes from './actionTypes';

export const setFlag = (flag, value) => ({
    type: actionTypes.SET_FLAG,
    flag,
    value,
});

export const clearFlags = () => ({
    type: actionTypes.CLEAR_FLAGS,
});

export const unsetFlag = (flag, value) => ({
    type: actionTypes.UNSET_FLAG,
    flag,
    value,
});
