import { pathToObjects } from '@webui-one/core/keypath/kp-parser';
import { compact, last, uniqPush } from '@webui-one/core/utils/array';
import * as S from '@webui-one/core/schema';

import { createEvaluatedSchema } from './evaluatedSchema';
import { createStaticSchema } from './staticSchema';

import {
    getName,
    parsePath,
    wrapWithData,
} from '../utils';

const appendRelativePath = parent => (rawSchema) => {
    const { path } = rawSchema;
    const { subPath } = parsePath(path);

    if (parent.isRoot) return rawSchema;

    if (subPath === undefined) return rawSchema;

    const schema = { ...rawSchema };

    if (schema.data.kind === 'case' && parent.ancestorRelativeKeypath) {
        schema.ancestorRelativeKeypath = parent.ancestorRelativeKeypath;
    }

    if (schema.data.kind === 'choice' && parent.data.kind !== 'case') {
        const { subPath: parentSubPath } = parsePath(parent.path);
        schema.ancestorRelativeKeypath = parent.relativeKeypath || parentSubPath || '.';
    }

    if (schema.data.kind === 'choice' && parent.data.kind === 'case') {
        schema.ancestorRelativeKeypath = parent.ancestorRelativeKeypath;
    }

    if (schema.data.kind !== 'choice' && parent.data.kind === 'case') {
        schema.relativeKeypath = schema.relativeName;
        if (parent.ancestorRelativeKeypath && parent.ancestorRelativeKeypath !== '.') {
            schema.relativeKeypath = `${parent.ancestorRelativeKeypath}/${schema.relativeName}`;
        }
    }

    if (parent.relativeKeypath && schema.data.kind !== 'choice' && schema.data.kind !== 'case') {
        schema.relativeKeypath = `${parent.relativeKeypath}/${schema.relativeName}`;
    }

    return schema;
};

const appendCaseAncestors = parent => (schema) => {
    const { subPath: parentSubPath } = parsePath(parent.path);

    if (parent.data.kind === 'case') {
        return {
            ...schema,
            caseAncestors: uniqPush(parentSubPath, parent.caseAncestors || []),
        };
    }

    if (parent.caseAncestors) {
        return {
            ...schema,
            caseAncestors: parent.caseAncestors,
        };
    }

    return schema;
};

const appendChoicePath = parent => (schema) => {
    if (parent.data.kind === 'case' && schema.data.kind === 'choice') {
        return {
            ...schema,
            choicePath: `${parent.choiceCasePath}/${schema.relativeName}`,
        };
    }

    if (parent.data.kind !== 'case' && schema.data.kind === 'choice') {
        return {
            ...schema,
            choicePath: schema.relativeName,
        };
    }

    if (schema.data.kind === 'case') {
        return {
            ...schema,
            choiceCasePath: `${parent.choicePath}/${schema.relativeName}`,
        };
    }
    return schema;
};

const createKindpath = ({ isRoot, kindpath = [] }) => (schema) => {
    const { data, relativeName: name, path } = schema;
    const { kind } = data;

    if (isRoot) {
        let rootPath = path;
        if (kind === 'list' && path.endsWith('{}')) {
            rootPath = path.replace(/\{\}$/, '');
        }
        return [{ kind, rootPath, name }];
    }

    return [...kindpath, { name, kind }];
};

const createBaseSchema = parent => (rawSchema) => {
    const { children, ...data } = rawSchema.data;

    let relativeName = getName(parent)(rawSchema);

    let path;

    if (parent.isRoot) {
        path = parent.path;
        relativeName = last(pathToObjects(path)).relativeName;
    }

    if (!parent.isRoot) path = `${parent.path}/${relativeName}`;
    if (data.kind === 'list') path = `${path}{}`;

    let schema = {
        data,
        ns: S.getPrefix(rawSchema),
        path,
        relativeName,
    };

    schema.kindpath = createKindpath(parent)(schema);
    schema = appendRelativePath(parent)(schema);
    schema = appendCaseAncestors(parent)(schema);
    schema = appendChoicePath(parent)(schema);

    return schema;
};

export const createParser = (rootPath, options = {}) => {
    const parse = parent => (rawSchema) => {
        const result = [];
        if (rawSchema.data.kind === 'access-denied') return [];

        const schema = createBaseSchema(parent)(rawSchema);

        const { children, cases } = rawSchema.data;

        // TODO: Fix for empty string children
        const children2 = children === '' ? [] : children;

        if (children !== undefined) {
            schema.children = children2.map(wrapWithData).map(getName(schema));
        }

        if (cases !== undefined) {
            schema.cases = cases.map(wrapWithData).map(getName(schema));
        }

        if (schema.data.kind === 'case') {
            schema.hasEvaluatedWhenEntries = S.hasEvaluatedWhenEntries(rawSchema.data);
        }

        if (options.includeSchema !== false) result.push(createStaticSchema(schema));
        if (options.includeEvaluated !== false) result.push(createEvaluatedSchema(parent)(schema));

        if (children) {
            result.push(...children.map(wrapWithData).map(parse(schema)));
        }

        if (cases) {
            result.push(...cases.map(wrapWithData).map(parse(schema)));
        }
        return compact(result);
    };

    return {
        parse,
    };
};

export {
    appendCaseAncestors as _appendCaseAncestors,
    appendChoicePath as _appendChoicePath,
    appendRelativePath as _appendRelativePath,
    createBaseSchema as _createBaseSchema,
};
