import { flatten, range1 } from './utils/array';
import { pathWithoutKeys } from './keypath/kp-parser';

const builtInTypesInfo = {
    binary: 'Any binary data',
    bits: 'A set of bits or flags',
    boolean: '"true" or "false"',
    decimal64: '64-bit signed decimal number',
    empty: 'A leaf that does not have any value',
    enumeration: 'Enumerated strings',
    identityref: 'A reference to an abstract identity',
    'instance-identifier': 'References a data tree node',
    int8: '8-bit signed integer',
    int16: '16-bit signed integer',
    int32: '32-bit signed integer',
    int64: '64-bit signed integer',
    leafref: 'A reference to a leaf instance',
    string: 'A string',
    uint8: '8-bit unsigned integer',
    uint16: '16-bit unsigned integer',
    uint32: '32-bit unsigned integer',
    uint64: '64-bit unsigned integer',
    union: 'Choice of member types',
};

const getTypeInfo = (typ) => {
    const infoType = (typ || []).find(t => t.info);
    if (infoType && infoType.info) return infoType.info;
    const typName = [...(typ || [])].reverse().find(t => t.name);

    if (typName) return builtInTypesInfo[typName.name];

    return undefined;
};


function instanceTypeName(item) {
    let tName;
    if (item.type && item.type.primitive) {
        tName = item.type.name;
    } else if (item.type && item.type.namespace) {
        tName = `${item.type.namespace}:${item.type.name}`;
    }
    return tName;
}

export function typeName(schema) {
    return instanceTypeName(schema.data);
}

export function type(schema) {
    return schema && schema.data && schema.data.type;
}

export function types(schema) {
    if (!('meta' in schema)) return undefined;
    if (!('types' in schema.meta)) return undefined;
    const typ = instanceTypeName(schema.data);
    return schema.meta.types[typ];
}

export const leafListType = (schema) => {
    const typ = types(schema).find(t => 'leaf_type' in t);
    if (typ && Array.isArray(typ.leaf_type) && typ.leaf_type.length > 0) {
        return typ.leaf_type[0];
    }
    return undefined;
};

export function hasChildren(schema) {
    const data = schema.data;
    return Array.isArray(data.children) && (data.children.length > 0);
}

export const hasFlags = schema => (
    schema &&
        schema.data &&
        Array.isArray(schema.data.flags) &&
        (schema.data.flags.length > 0)
);

export const flagIsSet = (flag, schema) =>
    hasFlags(schema) && schema.data.flags.includes(flag);

export function description(schema) {
    if (schema.data.description) {
        return schema.data.description;
    }
    return '';
}

export function info(schema) {
    if (schema && schema.data && schema.data.info && schema.data.info.string) {
        return schema.data.info.string;
    }
    return '';
}

export function minElements(schema) {
    return schema.data.min_elements;
}

export function maxElements(schema) {
    return schema.data.max_elements;
}

export function units(schema) {
    if (schema.data.units) {
        return schema.data.units;
    }
    return '';
}

export function kind(schema) {
    return schema && schema.data && schema.data.kind;
}

export const rangeType = (schema) => {
    if (Array.isArray(schema)) {
        return schema.find(t => t.range);
    }
    return (types(schema) || []).find(t => t.range);
};

export function rangeValues(schema) {
    const typ = rangeType(schema);
    if (typ && Array.isArray(typ.range.value)) {
        return flatten(
            typ.range.value.map((r) => {
                if (Array.isArray(r)) {
                    const [from, to] = r;
                    return range1(parseInt(from, 10), parseInt(to, 10));
                }
                return r;
            }),
        );
    }
    return undefined;
}

export const isRange = (schema) => {
    const typ = rangeType(schema);
    if (typ && Array.isArray(typ.range.value)) {
        return true;
    }
    return false;
};

export const rangeAsText = (schema) => {
    const typ = rangeType(schema);
    if (typ && Array.isArray(typ.range.value)) {
        return typ.range.value.map((r) => {
            if (Array.isArray(r)) {
                return r.join(' .. ');
            }
            return r;
        }).join(' | ');
    }
    return undefined;
};

export function isAction(schema) {
    return kind(schema) === 'action';
}

export function isActionInput(schema) {
    return schema && schema.data && schema.data.is_action_input;
}

export function isActionOutput(schema) {
    return schema && schema.data && schema.data.is_action_output;
}

export function isCase(schema) {
    return schema && schema.data && schema.data.kind === 'case';
}

export const isChoice = schema => schema && schema.data && schema.data.kind === 'choice';

export function isConfig(schema) {
    if (!schema || !schema.data) return false;
    return !(schema.data.config === false);
}

export function isOper(schema) {
    if (!schema || !schema.data) return false;
    return schema.data.config === false;
}

export const containsConfigChildren = (schema) => {
    if (!schema) return false;
    if (!schema.data) return false;
    if (!schema.data.children) return false;
    if (schema.data.children.length === 0) return false;
    if (!schema.data.flags) return true;
    if (!schema.data.flags.includes('CS_CHILD_READ_ONLY')) return true;
    return false;
};

export const containsOperChildren = (schema) => {
    if (!schema) return false;
    if (!schema.data) return false;
    if (!schema.data.children) return false;
    if (schema.data.children.length === 0) return false;
    if (!schema.data.flags) return false;
    if (schema.data.flags.includes('CS_CHILD_READ_ONLY')) return true;
    return false;
};

export const isOperOnly = (schema) => {
    if (!schema) return false;
    if (!schema.data) return false;
    if (schema.data.config === false && containsConfigChildren(schema) === false) return true;
    return false;
};

export const isConfigOnly = (schema) => {
    if (!schema) return false;
    if (!schema.data) return false;
    if (schema.data.config !== false && containsOperChildren(schema) === false) return true;
    return false;
};

export function isContainer(schema) {
    return schema && schema.data && schema.data.kind === 'container';
}

export const binaryType = schema => (
    typeName(schema) === 'base64Binary'
    || (types(schema) || []).find(t => t.name === 'base64Binary')
);

export const isBinary = schema => !!binaryType(schema);

export const bits = (schema) => {
    const typ = (types(schema) || []).find(t => t.bits);
    if (typ && Array.isArray(typ.bits)) {
        return typ.bits;
    }

    return undefined;
};

export const isBits = schema => !!bits(schema);

export const enumerationType = (schema) => {
    if (Array.isArray(schema)) {
        return schema.find(t => t.enumeration);
    }
    const schemaTypes = (types(schema) || []);

    const typeNode = schemaTypes.find(e => e.leaf_type);
    if (typeNode) {
        return typeNode.leaf_type.find(e => e.enumeration);
    }

    return schemaTypes.find(t => t.enumeration);
};

export const enumerationOptions = (schema) => {
    const enumType = enumerationType(schema);

    if (enumType && Array.isArray(enumType.enumeration)) {
        return enumType.enumeration;
    }

    return [];
};

export const isEnumeration = schema => !!enumerationType(schema);

export const booleanType = (schema) => {
    if (Array.isArray(schema)) {
        return schema.find(t => t.name === 'boolean');
    }
    return (types(schema) || []).find(t => t.name === 'boolean');
};

export const isBoolean = schema => !!booleanType(schema);

export const booleanOptions = schema => (
    isBoolean(schema) ? [{ label: 'true' }, { label: 'false' }] : undefined
);

export function isKey(schema) {
    return schema.data.kind === 'key';
}

export function isLeaf(schema) {
    return schema.data.kind === 'leaf';
}

export function isLeafRef(schema) {
    return schema.data.is_leafref === true;
}

export function isMandatory(schema) {
    return schema.data.mandatory === true;
}

export const isLeafList = schema => (
    !!(schema && schema.data && schema.data.kind === 'leaf-list')
);

export const isList = schema => (
    !!(schema && schema.data && schema.data.kind === 'list')
);

export const isListEntry = schema => (
    !!(schema && schema.data && schema.data.kind === 'list-entry')
);

export const isAccessDenied = schema => (
    !!(schema && schema.data && schema.data.kind === 'access-denied')
);

export function isCliMultilinePrompt(schema) {
    return flagIsSet('CLI_MULTI_LINE_PROMPT', schema);
}

export function isCliPreformatted(schema) {
    return flagIsSet('CLI_PREFORMATTED', schema);
}

export function isPresenceContainer(schema) {
    return !!(schema && schema.data && schema.data.presence === true);
}

export function isPrimitive(schema) {
    return type(schema).primitive === true;
}

export const unionTypes = (schema) => {
    const unionType = (types(schema) || []).find(t => t.union);
    if (unionType) return unionType.union;
    return undefined;
};

export const isUnion = (schema) => {
    const uTypes = unionTypes(schema);

    if (uTypes && Array.isArray(uTypes)) {
        return true;
    }
    return false;
};

export const unionOptionsFromUnionTypes = uTypes => (
    uTypes.reduce((acc, cur) => {
        if (isBoolean(cur)) {
            return [...acc, ...booleanOptions(cur)];
        }
        if (isEnumeration(cur)) {
            return [...acc, ...enumerationOptions(cur)];
        }
        return acc;
    }, [])
);

export const unionOptions = (schema) => {
    const uTypes = unionTypes(schema);
    if (!uTypes) return undefined;
    return unionOptionsFromUnionTypes(uTypes);
};

export const unionInfoOptions = (schema) => {
    const uTypes = unionTypes(schema);
    if (!uTypes) return undefined;
    return uTypes.reduce((acc, cur) => {
        if (isEnumeration(cur)) {
            return acc;
        }
        if (isRange(cur)) {
            acc.push(rangeAsText(cur));
            return acc;
        }
        const typeInfo = getTypeInfo(cur);
        if (typeInfo) {
            acc.push(typeInfo);
            return acc;
        }

        return acc;
    }, []);
};

export const unionIncludesEnumeration = (schema) => {
    const uTypes = unionTypes(schema);
    return uTypes.findIndex(t => isEnumeration(t)) > 0;
};

export const isEmptyLeaf = schema => isLeaf(schema) && (type(schema) || {}).name === 'empty';

export const selection = (schema) => {
    if (isLeafList(schema)) { return [0]; }
    if (schema && Array.isArray(schema.children)) {
        return schema.children.map(child => child.name);
    }
    return [];
};

export function keypath(schema) {
    return schema && schema.meta && schema.meta.keypath;
}

export function nodepath(schema) {
    return schema.meta && schema.meta.nodepath;
}

export function isParent(schema) {
    switch (kind(schema)) {
    case 'action':
    case 'container':
    case 'list':
    case 'list-entry':
        return true;
    default:
        return false;
    }
}

export function leafrefTarget(schema) {
    return schema.data.leafref_target;
}

export function name(schema) {
    return schema && schema.data && schema.data.name;
}

export function namespace(schema) {
    return schema.meta.namespace;
}

export function parentKeypath(schema) {
    return schema.meta.parentKeypath;
}

export function prefix(schema) {
    return schema.meta.prefix;
}

export function qname(schema) {
    return schema && schema.data && schema.data.qname;
}

export const defaultValue = schema => schema.data.default;

export const exists = schema => (
    schema && schema.data && schema.data.exists === true
);

function buildChildKeypath(parentKind, parentPath, parentPrefix, child) {
    if (parentKind === 'module') {
        return `/${child.qname}`;
    }
    const pPath = pathWithoutKeys(parentPath);

    if (child.qname && !child.qname.startsWith(`${parentPrefix}:`)) {
        return `${pPath}/${child.qname}`;
    }
    return `${pPath}/${child.name}`;
}

const getChildPrefix = (child) => {
    const qn = child.qname || '';

    if (qn.includes(':')) {
        return qn.split(':')[0];
    }
    return undefined;
};

export const getPrefix = (schema) => {
    const pfx = getChildPrefix(schema.data);
    if (pfx) return pfx;
    const re = /\/[^:]+:/g;
    const matches = (schema.meta.keypath || '').match(re);
    if (matches && matches.length > 0) {
        return matches.pop().replace(/\/|:/g, '');
    }
    return schema.meta.prefix;
};

export const getKeys = (schema) => {
    if (isLeafList(schema)) { return [0]; }
    if (schema && schema.data && Array.isArray(schema.data.key)) { return schema.data.key; }
    return [];
};

const isActionInputChoice = (schema) => {
    const firstChild = schema.cases[0].children[0];
    if (firstChild === 'choice') {
        return isActionInputChoice(firstChild);
    }
    return !!firstChild.is_action_input;
};

export function childInstance(parentPrefix, parentKind, parentPath, parentNodepath, child) {
    let pNodepath = parentNodepath || parentPath;

    if (parentKind === 'choice' || parentKind === 'case') {
        pNodepath = pNodepath.substring(0, pNodepath.lastIndexOf('/'));
    }

    const kPath = buildChildKeypath(parentKind, parentPath, parentPrefix, child);
    const nPath = buildChildKeypath(parentKind, pNodepath, parentPrefix, child);

    const childPrefix = getChildPrefix(child);

    const childSchema = {
        data: child,
        meta: {
            prefix: childPrefix || parentPrefix,
            keypath: kPath,
            parentKeypath: parentPath,
            types: {},
        },
    };

    if (parentKind === 'action' && child.kind === 'choice') {
        if (isActionInputChoice(child)) {
            childSchema.data.is_action_input = true;
        } else {
            childSchema.data.is_action_output = true;
        }
    }

    if (nPath !== kPath) {
        childSchema.meta.nodepath = nPath;
    }

    if (parentPath !== pNodepath) {
        childSchema.meta.parentNodepath = pNodepath;
    }

    return childSchema;
}

export function children(schema) {
    const data = schema.data;
    let ret = [];

    const parentKind = kind(schema);
    const parentPrefix = getPrefix(schema);
    const pKeypath = pathWithoutKeys(keypath(schema));
    const pNodepath = nodepath(schema) || pathWithoutKeys(keypath(schema));

    if (Array.isArray(data.children)) {
        ret = data.children.reduce((acc, cur) => {
            // Skip action output, remove if when fixing ENG-16962
            if (cur.is_action_output) return acc;

            const inst = childInstance(
                parentPrefix,
                parentKind,
                pKeypath,
                pNodepath,
                cur,
            );
            acc.push(inst);
            return acc;
        }, []);
    }

    return ret;
}

export const hasEvaluatedWhenEntries = ca =>
    Array.isArray(ca.children) &&
        !ca.children.every(child => child.evaluated_when_entry === false);

export function cases(schema) {
    const data = schema.data;
    let ret = [];

    const parentKind = kind(schema);
    const parentPrefix = getPrefix(schema);
    const pKeypath = pathWithoutKeys(keypath(schema));
    const pNodepath = nodepath(schema) || pathWithoutKeys(keypath(schema));

    if (data.cases && Array.isArray(data.cases)) {
        ret = data.cases
            .reduce((acc, cur) => {
                const inst = childInstance(
                    parentPrefix,
                    parentKind,
                    pKeypath,
                    pNodepath,
                    cur,
                );
                inst.data.hasEvaluatedWhenEntries = hasEvaluatedWhenEntries(cur);
                acc.push(inst);
                return acc;
            }, []);
    }
    return ret;
}

export function childByName(n, schema) {
    return children(schema).find(c => name(c) === n);
}

export function childByQName(n, schema) {
    return children(schema).find(c => qname(c) === n);
}

export function childrenKeypaths(schema) {
    return children(schema).map(c => keypath(c));
}

export function childrenOfKind(k, schema) {
    return children(schema).filter(c => kind(c) === k);
}

export function keys(schema) {
    return childrenOfKind('key', schema);
}

export function keyNames(schema) {
    return schema.data.key;
}

export function mandatory(schema) {
    return schema.data.mandatory;
}

export const evaluatedWhenEntry = schema => (
    schema && schema.data && schema.data.evaluated_when_entry
);

const access = schema => (
    schema && schema.data && schema.data.access ? schema.data.access : {}
);

export const deletable = (schema) => {
    const deleteFlag = access(schema).delete;
    // TODO: ENG-22224 access flag should be delete true, remove after jira is fixed
    if (deleteFlag === false && isActionInput(schema)) return true;
    return deleteFlag === true;
};

export const suppressEcho = schema => (
    schema && schema.data && schema.data.suppress_echo === true
);

export const isConfigFalseCallpoint = schema => (
    schema && schema.data && schema.data.is_config_false_callpoint === true
);

export const deps = schema => schema && schema.data && schema.data.deps;

export class Schema {
    constructor(schema) {
        this.schema = schema;
    }

    get deletable() { return deletable(this.schema); }
    get isConfigFalseCallpoint() { return isConfigFalseCallpoint(this.schema); }
    get children() { return children(this.schema); }
    get defaultValue() { return defaultValue(this.schema); }
    get description() { return description(this.schema); }
    get exists() { return exists(this.schema); }
    get hasChildren() { return hasChildren(this.schema); }
    get hasFlags() { return hasFlags(this.schema); }
    get info() { return info(this.schema); }
    get isConfig() { return isConfig(this.schema); }
    get isOper() { return isOper(this.schema); }
    get isContainer() { return isContainer(this.schema); }
    get isBinary() { return isBinary(this.schema); }
    get isBits() { return isBits(this.schema); }
    get isEmptyLeaf() { return isEmptyLeaf(this.schema); }
    get isEnumeration() { return isEnumeration(this.schema); }
    get isBoolean() { return isBoolean(this.schema); }
    get isRange() { return isRange(this.schema); }
    get isKey() { return isKey(this.schema); }
    get isLeaf() { return isLeaf(this.schema); }
    get isLeafList() { return isLeafList(this.schema); }
    get isLeafRef() { return isLeafRef(this.schema); }
    get isList() { return isList(this.schema); }
    get isCliMultilinePrompt() { return isCliMultilinePrompt(this.schema); }
    get isParent() { return isParent(this.schema); }
    get isCliPreformatted() { return isCliPreformatted(this.schema); }
    get isPresenceContainer() { return isPresenceContainer(this.schema); }
    get isPrimitive() { return isPrimitive(this.schema); }
    get keys() { return keys(this.schema); }
    get keyNames() { return keyNames(this.schema); }
    get mandatory() { return mandatory(this.schema); }
    get keypath() { return keypath(this.schema); }
    get kind() { return kind(this.schema); }
    get leafrefTarget() { return leafrefTarget(this.schema); }
    get name() { return name(this.schema); }
    get namespace() { return namespace(this.schema); }
    get parentKeypath() { return parentKeypath(this.schema); }
    get prefix() { return prefix(this.schema); }
    get qname() { return qname(this.schema); }
    get selection() { return selection(this.schema); }
    get suppressEcho() { return suppressEcho(this.schema); }
    get type() { return type(this.schema); }
    get types() { return types(this.schema); }
    get typeName() { return typeName(this.schema); }
    get units() { return units(this.schema); }

    getChildrenOfKind(k) { return childrenOfKind(k, this.schema); }
    flagIsSet(f) { return flagIsSet(f, this.schema); }
}

export {
    buildChildKeypath as _buildChildKeypath,
    instanceTypeName as _instanceTypeName,
    getTypeInfo as _getTypeInfo,
};
