import * as dataActionTypes from 'modules/data/actionTypes';
import * as schemaActionTypes from 'modules/schemas/actionTypes';
import * as schemas2ActionTypes from 'modules/schemas2/actionTypes';
import * as servicesActionTypes from 'modules/services/actionTypes';

export default function types(state = {}, action) {
    switch (action.type) {
    case schemaActionTypes.GET_SCHEMA_COMPLETE:
        return { ...state, ...action.schema.meta.types };
    case schemas2ActionTypes.GET_SCHEMAS_COMPLETE:
        return { ...state, ...action.types };
    case dataActionTypes.GET_DATA_COMPLETE:
        return { ...state, ...action.types };
    case servicesActionTypes.GET_SERVICE_POINT_SCHEMAS_COMPLETE:
        return { ...state, ...action.types };
    default:
        return state;
    }
}

const emptyObject = Object.freeze({});
export const selectTypes = state => (state && state.types) || emptyObject;

export const selectType = (state, type) => (
    selectTypes(state)[type]
);
