import {
    pathWithoutKeyInstances,
    pathToObjects,
} from '@webui-one/core/keypath/kp-parser';
import { isString } from '@webui-one/core/utils/string';
import * as KPI from '@webui-one/core/kindpath/kindpathItem';

import { itemTypes } from './schemas';

export const valueOrDefault = (value, defaultValue) => (
    value === undefined ? defaultValue : value
);

export const getPrefixFromPath = (path) => {
    const objs = pathToObjects(path);
    return objs.slice(-1)[0].ns;
};

export const getName = parent => (schema) => {
    const prefix = isString(parent) ? getPrefixFromPath(parent) : parent.ns;
    const { name, qname } = schema.data;
    const relativeName = qname.startsWith(`${prefix}:`) ? name : qname;

    if (schema.data.is_action_output === true) {
        return `${relativeName}__@is_action_output`;
    }
    return relativeName;
};

export const wrapWithData = item => ({ data: item });

export const parsePath = (path) => {
    const keyEnd = path.lastIndexOf('}');
    if (keyEnd > 0) {
        // List-instance
        const rootPath = path.substring(0, keyEnd + 1);
        const subPath = path.substring(keyEnd + 2) || '.';

        return {
            rootPath,
            rootSchemaPath: pathWithoutKeyInstances(rootPath),
            subPath,
        };
    }

    const firstSlash = path.indexOf('/', 1);
    if (firstSlash === -1) {
        // Container on the top
        return {
            rootPath: path,
            rootSchemaPath: path,
            subPath: '.',
        };
    }

    const rootPath = path.substring(0, firstSlash);
    const subPath = path.substring(firstSlash + 1) || '.';

    return {
        // Descendant to container on top
        rootPath,
        rootSchemaPath: rootPath,
        subPath,
    };
};

export const pathConcat = parentPath => childName => `${parentPath}/${childName}`;

export const isDeviceConfigChild = path => /^\/ncs:devices\/device\{[^}]+\}\/config\/.*$/.test(path);
export const isDeviceConfig = path => /^\/ncs:devices\/device\{[^}]+\}\/config$/.test(path);
export const isDevice = path => /^\/ncs:devices\/device\{[^}]+\}$/.test(path);

export const parsePathByItemType = (schema) => {
    const { itemType, path } = schema;
    const { rootPath, rootSchemaPath, subPath } = parsePath(path);
    if (itemType === itemTypes.STATIC_SCHEMA || itemType === itemTypes.STATIC_SYMLINK) {
        return { rootPath: rootSchemaPath, subPath };
    }
    return { rootPath, subPath };
};

const curlyBracesPairInEndRE = /{[^}]*}$/;

const getListEntrySchemaPath = ({ path }) => {
    const schemaPath = pathWithoutKeyInstances(path);
    return schemaPath.endsWith('}') ? schemaPath : `${schemaPath}{}`;
};

const getListSchemaPath = ({ path }) => {
    const schemaPath = pathWithoutKeyInstances(path);
    return schemaPath.endsWith('}') ? schemaPath.replace(curlyBracesPairInEndRE, '') : schemaPath;
};

const isFirst = ix => ix === 0;
const isLast = (ix, arr) => ix === arr.length - 1;
const isNotLast = (ix, arr) => ix < arr.length - 1;
const isNotTop = (ix, arr) => isFirst(ix) && arr.length > 1;

const getListEntryPath = ({ kindpath }) => kindpath.reduce((acc, cur, ix) => {
    if (KPI.isChoice(cur) || KPI.isCase(cur)) return acc;
    const { name, rootPath } = cur;

    if (isFirst(ix) && rootPath && KPI.isList(cur)) return `${rootPath}{}`;
    if (isFirst(ix) && rootPath) return rootPath;

    if (KPI.isList(cur)) {
        return `${acc}/${name}{}`;
    }
    return `${acc}/${name}`;
}, '');


const getListPath = ({ kindpath }) => kindpath.reduce((acc, cur, ix, arr) => {
    if (KPI.isChoice(cur) || KPI.isCase(cur)) return acc;
    const { name: rawName, rootPath } = cur;

    if (isNotTop(ix, arr) && rootPath && KPI.isList(cur)) {
        return `${rootPath}{}`;
    }
    if (isFirst(ix) && rootPath) return rootPath;

    let name = rawName;

    if (isLast(ix, arr) && (KPI.isList(cur) || KPI.isListEntry(cur))) {
        name = name.replace(curlyBracesPairInEndRE, '');
        return `${acc}/${name}`;
    }

    if (isNotLast(ix, arr) && KPI.isList(cur)) {
        if (curlyBracesPairInEndRE.test(name) === false) {
            name = `${name}{}`;
        }
        return `${acc}/${name}`;
    }

    return `${acc}/${name}`;
}, '');

export const getListPaths = (schema) => {
    const listEntrySchemaPath = getListEntrySchemaPath(schema);
    const listSchemaPath = getListSchemaPath(schema);

    const listEntryPath = getListEntryPath(schema);

    const {
        rootPath: listRootPath,
        subPath: listSubPath,
    } = parsePath(getListPath(schema));

    const {
        subPath: listEntrySubPath,
    } = parsePath(listEntryPath);

    const {
        rootSchemaPath: listRootSchemaPath,
        subPath: listSubSchemaPath,
    } = parsePath(listSchemaPath);

    const {
        rootSchemaPath: listEntryRootSchemaPath,
        subPath: listEntrySubSchemaPath,
    } = parsePath(listEntrySchemaPath);


    const listPath = listSubPath && listSubPath !== '.'
        ? `${listRootPath}/${listSubPath}` : listRootPath;

    return ({
        listRootSchemaPath, //      /jiras:ENG-17204
        listRootPath, //            /jiras:ENG-17204
        listSubSchemaPath, //       choice-1/case-1/list-1
        listSubPath, //             list-1,
        listEntryRootSchemaPath, // /jiras:ENG-17204/choice-1/case-1/list-1{}
        listEntrySubPath, //        list-1{foo}
        listEntrySubSchemaPath, //  .
        listSchemaPath, //          /jiras:ENG-17204/choice-1/case-1/list-1
        listEntrySchemaPath, //     /jiras:ENG-17204/choice-1/case-1/list-1{}
        listPath, //                /jiras:ENG-17204/list-1
        listEntryPath, //           /jiras:ENG-17204/list-1{foo}
    });
};

export {
    getListEntryPath as _getListEntryPath,
    getListPath as _getListPath,
    getListEntrySchemaPath as _getListEntrySchemaPath,
    getListSchemaPath as _getListSchemaPath,
};

