import * as S from '@webui-one/core/schema';
import {
    selectSchema,
} from 'modules/schemas/reducer';
import { selectIsConfigLoading } from 'modules/data/selectors';

import {
    DBS,
    MODES,
    TAGS,
} from '@webui-one/core/jsonrpc/constants';

import { transactionStatus } from './constants';

export const getTransactions = state => (
    state && state.transactions ? state.transactions : {}
);

const getTrans = state => getTransactions(state).trans || {};

export const selectTrans = (
    state,
    {
        actionPath = undefined,
        db = DBS.RUNNING,
        mode,
        tag = TAGS.WEBUI_ONE,
        path,
    } = {},
) => {
    const trans = getTrans(state);

    if (actionPath) {
        return Object.values(trans).find(t => t.actionPath === actionPath && t.db === DBS.CS_TRANS);
    }

    return Object.keys(trans).reduce((acc, key) => {
        const val = trans[key];

        if (path && path.startsWith(val.actionPath)) {
            return val;
        }
        if (acc === undefined && val.db === db && val.tag === tag && val.mode === mode) {
            return val;
        }
        return acc;
    }, undefined);
};

const selectReadTrans = (state, args) => (
    selectTrans(state, { ...args, mode: MODES.READ })
);

const selectReadWriteTrans = (state, args) => (
    selectTrans(state, { ...args, mode: MODES.READ_WRITE }) || {}
);

export const selectReadTh = (state, args) => (selectReadTrans(state, args) || {}).th;
export const selectReadWriteTh = (state, args) => (selectReadWriteTrans(state, args) || {}).th;


export const selectChanges = state => selectReadWriteTrans(state).changes;
export const selectNumberOfChanges = state => (selectChanges(state) || []).length;
export const selectCommitInProgress = state => selectReadWriteTrans(state).commitInProgress;
export const selectCommitDryRunInProgress = state => (
    selectReadWriteTrans(state).commitDryRunInProgress
);
export const selectCommitDryRunResult = state => selectReadWriteTrans(state).commitDryRunResult;
export const selectCommitDryRunError = state => selectReadWriteTrans(state).commitDryRunError;
export const selectError = state => selectReadWriteTrans(state).error;
export const selectHasLatestCommitDryRunResult = (state) => {
    const { hasLatestCommitDryRunResult, commitDryRunResult } = selectReadWriteTrans(state);
    if (commitDryRunResult && Object.values(commitDryRunResult).length > 0) {
        return hasLatestCommitDryRunResult;
    }
    return false;
};

export const selectNativeCommitDryRunInProgress = state =>
    selectReadWriteTrans(state).nativeCommitDryRunInProgress;
export const selectNativeCommitDryRunResult = state =>
    selectReadWriteTrans(state).nativeCommitDryRunResult;
export const selectNativeCommitDryRunError = state =>
    selectReadWriteTrans(state).nativeCommitDryRunError;
export const selectRead = state => selectReadWriteTh(state);
export const selectReadWrite = state => selectReadWriteTh(state);
export const selectReadWriteOrRead = state => selectReadWrite(state) || selectRead(state);
export const selectResolveInProgress = state => (
    Object.values(getTrans(state)).reduce((acc, trans) => {
        if (trans && trans.thIsBusy) return true;
        return acc;
    }, false)
);

export const selectThIsBusy = state => ((
    (selectReadWriteTrans(state) || {}).thIsBusy ||
    selectIsConfigLoading(state) ||
    selectNativeCommitDryRunInProgress(state) ||
    selectCommitDryRunInProgress(state) ||
    selectCommitInProgress(state) ||
    selectResolveInProgress(state)
));
export const selectStatus = state => selectReadWriteTrans(state).status;
export const selectValid = state => selectStatus(state) !== transactionStatus.INVALID;
export const selectValidateTransactionError = state => (
    selectReadWriteTrans(state).validateTransError
);
export const selectWarnings = state => selectReadWriteTrans(state).warnings;
export const selectCommitOptions = state => getTransactions(state).commitOptions;
export const selectRollbacks = state => getTransactions(state).rollbacks;
export const selectUpgradePending = state => getTransactions(state).upgradePending;
export const selectLoadInProgress = state => getTransactions(state).loadInProgress;
export const selectLoadFromFileError = state => getTransactions(state).loadFromFileError;
export const selectLoadFromTextError = state => getTransactions(state).loadFromTextError;
export const selectSaveToFileError = state => getTransactions(state).saveToFileError;

export const selectChangesWithDefaultValues = (
    state,
    {
        funcs: {
            selectChangesFunc = selectChanges,
            selectSchemaFunc = selectSchema,
        } = {},
    } = {},
) => {
    const changes = selectChangesFunc(state);
    return (changes || []).map((c) => {
        if (c.op === 'default_set') {
            const schema = selectSchemaFunc(state, c.path);
            if (schema !== undefined && S.defaultValue(schema) !== undefined) {
                const value = `(${S.defaultValue(schema)})`;
                return {
                    ...c,
                    value,
                };
            }
        }
        return c;
    });
};

export const selectConflicts = state => (
    selectReadWriteTrans(state).conflicts
);

export const transactionIsEmpty = (state) => {
    const changes = selectReadWriteTrans(state).changes;
    return !Array.isArray(changes) || changes.length < 1;
};

export const selectPendingRead = state => getTransactions(state).pendingRead;
export const selectWritePending = state => getTransactions(state).writePending;
