import { quoteKey } from '@webui-one/core/functional_utils';

export const devicesRootPath = '/ncs:devices';
export const deviveGroupListPath = `${devicesRootPath}/device-group`;
export const deviceListPath = `${devicesRootPath}/device`;
export const deviceTypeSubPath = 'device-type/*/ned-id';
export const alarmsPath = '/al:alarms/alarm-list/alarm';

export const getDeviceListKeyPath = deviceName => `${deviceListPath}{${quoteKey(deviceName)}}`;

export const getDeviceGroupDeviceListPath = groupName =>
    `${deviveGroupListPath}{${quoteKey(groupName)}}/device-name`;

export const getDeviceGroupDeviceListDevicePath = (groupName, deviceName) =>
    `${getDeviceGroupDeviceListPath(groupName)}{${quoteKey(deviceName)}}`;

// Ordered from most critical to unclassified
export const ALARM_PRECEDENCE = [
    './alarm-summary/criticals',
    './alarm-summary/majors',
    './alarm-summary/minors',
    './alarm-summary/warnings',
    './alarm-summary/indeterminates',
];

export const ALARM_STATUS = {
    CRITICALS: 'criticals',
    MAJORS: 'majors',
    MINORS: 'minors',
    WARNINGS: 'warnings',
    INDETERMINATES: 'indeterminates',
    CLEARED: 'cleared',
};

export const DEVICE_MANAGER_USER_STORAGE_PATH = 'DEVICE_MANAGER_USER_STORAGE_PATH';

export const COLUMNS = {
    ADDRESS: 'address',
    NAME: 'name',
    PORT: 'port',
    TYPE: 'device-type/*/ned-id',
    SERVICES: 'count(./service-list)',
};

// These are mapped 1:1 in device manager
export const COLUMNS_SELECTION = [
    {
        key: COLUMNS.NAME,
        title: 'name',
    },
    {
        key: COLUMNS.ADDRESS,
        title: 'address',
    },
    {
        key: COLUMNS.PORT,
        title: 'port',
    },
    {
        key: COLUMNS.TYPE,
        title: 'type',
    },
    {
        key: COLUMNS.SERVICES,
        title: 'services',
    },
];

// Not mapped 1:1.
export const COLUMNS_CUSTOM = [
    {
        key: 'alarm',
        title: 'alarm',
    },
    {
        key: 'config',
        title: 'configuration',
    },
    {
        key: 'expandable',
        title: 'expandable',
        canBeHidden: false,
        isExpandable: true,
    },
];

export const DEVICE_ACTIONS = [
    'ping',
    'connect',
    'check-sync',
    'sync-from',
    'sync-to',
    'compare-config',
];

export const COLUMNS_ACTION = DEVICE_ACTIONS.map(k => ({
    key: k,
    title: k,
}));

export const DEVICE_COLUMNS = [
    ...COLUMNS_SELECTION,
    ...COLUMNS_CUSTOM,
    ...COLUMNS_ACTION,
].map(c => ({ ...c, canBeHidden: c.canBeHidden === undefined }));

export const QUERY_SELECTION = [
    ...COLUMNS_SELECTION.map(c => c.key),
    ...ALARM_PRECEDENCE,
];
