import {
    selectSchema as _selectSchema,
    toKeypath,
} from 'modules/schemas/reducer';
import {
    selectCaseValue as _selectCaseValue,
    selectNode as _selectNode,
} from 'modules/nodes/selectors';
import * as S from '@webui-one/core/schema';
import { isEmpty } from '@webui-one/core/utils/object';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import {
    isEmptyArray,
} from '@webui-one/core/utils/array';

export const selectYangActions = state => state.yangActions || {};

export const selectYangActionNode = (state, path) => (
    selectYangActions(state)[path] || {}
);

export const selectResult = (state, path) => (
    selectYangActionNode(state, path).result
);

export const selectStartedAt = (state, path) => (
    selectYangActionNode(state, path).startedAt
);

export const selectCompletedAt = (state, path) => (
    selectYangActionNode(state, path).completedAt
);

export const selectResultPending = (state, path) => (
    selectYangActionNode(state, path).resultPending
);

export const selectError = (state, path) => (
    selectYangActionNode(state, path).error
);

const selectValue = (
    state,
    {
        path,
        funcs: {
            selectSchema = _selectSchema,
            selectNode = _selectNode,
        } = {},
    } = {},
) => {
    const schema = selectSchema(state, path);
    const nodePath = schema.meta.nodepath || path;
    const node = selectNode(state, nodePath);
    return (node.newValue || node.value) || node.exists;
};

const selectCase = (
    state,
    {
        path,
        funcs: {
            selectSchema = _selectSchema,
            // eslint-disable-next-line no-use-before-define
            selectInputParamsFunc = selectInputParams,
        } = {},
    } = {},
) => {
    const schema = selectSchema(state, path);
    const choiceRoot = schema.meta.parentNodePath;

    return schema.children.reduce((acc, cur) => {
        const keypath = toKeypath(path, cur.path);
        return { ...acc, ...selectInputParamsFunc(state, { path: keypath, choiceRoot }) };
    }, {});
};

const selectChoice = (
    state,
    {
        path,
        funcs: {
            selectSchema = _selectSchema,
            selectCaseValue = _selectCaseValue,
            selectCaseFunc = selectCase,
        } = {},
    } = {},
) => {
    const schema = selectSchema(state, path);
    const caseValue = selectCaseValue(state, path);
    if (!caseValue) return undefined;

    return schema.cases.reduce((acc, cur) => {
        if (caseValue !== cur.name) return acc;
        const keypath = toKeypath(path, cur.path);
        return { ...acc, ...selectCaseFunc(state, { path: keypath }) };
    }, {});
};

const selectContainer = (
    state,
    {
        path,
        choiceRoot,
        funcs: {
            selectSchema = _selectSchema,
        } = {},
    } = {},
) => {
    const schema = selectSchema(state, path);
    if (!schema.children) return {};
    return schema.children.reduce((acc, cur) => {
        const keypath = toKeypath(path, cur.path);
        // eslint-disable-next-line no-use-before-define
        return { ...acc, ...selectInputParams(state, { path: keypath, choiceRoot }) };
    }, {});
};

export const selectList = (
    state,
    {
        path,
        funcs: {
            selectSchema = _selectSchema,
            selectNode = _selectNode,
        } = {},
    } = {},
) => {
    const schema = selectSchema(state, path);
    const name = S.name(schema);
    const node = selectNode(state, path);
    return { [name]: node.rows };
};

export const selectLeafList = (
    state,
    {
        path,
        funcs: {
            selectSchema = _selectSchema,
            selectNode = _selectNode,
        } = {},
    },
) => {
    const schema = selectSchema(state, path);
    const name = S.name(schema);
    const node = selectNode(state, path);
    const rows = Array.isArray(node.rows)
        ? node.rows.map(r => r[0])
        : [];
    return { [name]: rows };
};


export const selectInputParams = (
    state,
    {
        path,
        choiceRoot,
        funcs: {
            selectSchema = _selectSchema,
            selectValueFunc = selectValue,
            selectContainerFunc = selectContainer,
            selectChoiceFunc = selectChoice,
            selectListFunc = selectList,
            selectLeafListFunc = selectLeafList,
            selectInputParamsFunc = selectInputParams,
        } = {},
    } = {},
) => {
    const schema = selectSchema(state, path);
    if (!schema) return undefined;
    const kind = S.kind(schema);
    const name = S.name(schema);
    if (S.isActionOutput(schema)) return undefined;

    if (kind === 'leaf') {
        const value = selectValueFunc(state, { path });

        return value ? { [name]: value } : undefined;
    }

    if (kind === 'container') {
        const container = selectContainerFunc(state, { path, choiceRoot });
        return isEmpty(container) ? undefined : { [name]: container };
    }

    if (kind === 'choice') {
        return selectChoiceFunc(state, { path });
    }

    if (kind === 'list' || kind === 'list-entry') {
        return selectListFunc(state, { path });
    }

    if (kind === 'leaf-list') {
        return selectLeafListFunc(state, { path });
    }

    if (!schema.children) return undefined;

    return schema.children.reduce((acc, cur) => {
        const keypath = toKeypath(path, cur.path);
        return { ...acc, ...selectInputParamsFunc(state, { path: keypath }) };
    }, {});
};

export const ACTION_STATUS = Object.freeze({
    RESULT_PENDING: { message: 'result is pending...', color: defaultStyles.color15 },
    RESULT_SUCCESS: { message: 'yes', color: defaultStyles.color14 },
    RESULT_FAIL: { message: 'no', color: defaultStyles.color5 },
    RESULT_NA: { message: 'N/A', color: defaultStyles.color9 },
    RESULT_UNKNOWN: { message: 'unknown', color: defaultStyles.color9 },
    RESULT_ERROR: { message: 'error', color: defaultStyles.color5 },
    RESULT_PERMISSION_DENIED: { message: 'permission denied', color: defaultStyles.color9 },
    RESULT_NO_SUPPORT: { message: 'operation not supported', color: defaultStyles.color9 },
    RESULT_IN_COMMIT_QUEUE: { message: 'in-commit-queue', color: defaultStyles.color9 },
    RESULT_PING_FOUND: { message: 'device was found', color: defaultStyles.color14 },
    RESULT_PING_NOT_FOUND: { message: 'device was not found', color: defaultStyles.color5 },
    RESULT_CHECK_SYNC_IN_SYNC: { message: 'device was in sync', color: defaultStyles.color14 },
    RESULT_CHECK_SYNC_NOT_IN_SYNC:
        { message: 'device was not in sync', color: defaultStyles.color5 },
    RESULT_CONNECT_SUCCESS: { message: 'device was connected', color: defaultStyles.color14 },
    RESULT_CONNECT_FAIL: { message: 'device was not connected', color: defaultStyles.color9 },
    RESULT_RE_DEPLOY_SUCCESS: { message: 'service was re-deployed', color: defaultStyles.color14 },
    RESULT_RE_DEPLOY_FAIL: { message: 'service was not re-deployed', color: defaultStyles.color9 },
    RESULT_RE_DEPLOY_DRY_RUN_DIFF: { message: 'service has changes', color: defaultStyles.color17 },
    RESULT_RE_DEPLOY_DRY_RUN_NO_DIFF: { message: 'service has no changes', color: defaultStyles.color14 },
    RESULT_CHECK_SERVICE_IN_SYNC_IN_SYNC: { message: 'service was in sync', color: defaultStyles.color14 },
    RESULT_CHECK_SERVICE_IN_SYNC_NOT_IN_SYNC: { message: 'service was not in sync', color: defaultStyles.color5 },
    RESULT_SYNC_FROM_SUCCESS: { message: 'config was synced from device', color: defaultStyles.color14 },
    RESULT_SYNC_FROM_FAIL: { message: 'config was not synced from device', color: defaultStyles.color5 },
    RESULT_SYNC_TO_SUCCESS: { message: 'config was synced to device', color: defaultStyles.color14 },
    RESULT_SYNC_TO_FAIL: { message: 'config was not synced to device', color: defaultStyles.color5 },
    RESULT_COMPARE_CONFIG_DIFF: { message: 'CDB and device config does not match', color: defaultStyles.color17 },
    RESULT_COMPARE_CONFIG_NO_DIFF: { message: 'CDB and device config matches', color: defaultStyles.color14 },
});

export const getResultFactory = nameKey => arr => (arr.find(r => r.name === nameKey) || {}).value;
export const getResult = getResultFactory('result');
export const getDiffResult = getResultFactory('diff');
export const getInfoResult = getResultFactory('info');

export const getPingStatus = (results) => {
    const result = getResult(results) || '';
    if (result.indexOf('icmp_seq=') > -1) return ACTION_STATUS.RESULT_PING_FOUND;
    if (result.indexOf('ping statistics') > -1) return ACTION_STATUS.RESULT_PING_NOT_FOUND;
    if (result.indexOf('command not found') > -1) return ACTION_STATUS.RESULT_NO_SUPPORT;
    return ACTION_STATUS.RESULT_UNKNOWN;
};
export const getConnectionStatus = (results) => {
    const result = getResult(results) || '';
    if (result === 'true') return ACTION_STATUS.RESULT_CONNECT_SUCCESS;
    return ACTION_STATUS.RESULT_CONNECT_FAIL;
};
export const getCheckSyncStatus = (results) => {
    const result = getResult(results) || '';
    if (Object.values(ACTION_STATUS).indexOf(result) > -1) return result;
    if (result === 'in-sync') return ACTION_STATUS.RESULT_CHECK_SYNC_IN_SYNC;
    if (result === 'out-of-sync') return ACTION_STATUS.RESULT_CHECK_SYNC_NOT_IN_SYNC;
    return ACTION_STATUS.RESULT_UNKNOWN;
};
export const getSyncFromStatus = (results) => {
    const result = getResult(results) || '';
    if (result === 'true') return ACTION_STATUS.RESULT_SYNC_FROM_SUCCESS;
    if (result === 'false') return ACTION_STATUS.RESULT_SYNC_FROM_FAIL;
    return ACTION_STATUS.RESULT_UNKNOWN;
};
export const getCompareConfigStatus = (results) => {
    const res = getDiffResult(results);
    if (res) return ACTION_STATUS.RESULT_COMPARE_CONFIG_DIFF;
    if(isEmptyArray(results)) return ACTION_STATUS.RESULT_COMPARE_CONFIG_NO_DIFF;
    return ACTION_STATUS.RESULT_UNKNOWN;
};
export const getActionStatus = (
    actions = {},
    actionKey,
    getSpecificActionStatus) => {
    const action = actions[actionKey];
    if (!action) return { actionStatus: ACTION_STATUS.RESULT_NA, timestamp: 0 };
    if (action.resultPending) {
        return { actionStatus: ACTION_STATUS.RESULT_PENDING, timestamp: action.startedAt };
    }
    if (action.error) {
        if (action.error.message === 'Permission denied') {
            return {
                actionStatus: ACTION_STATUS.RESULT_PERMISSION_DENIED,
                timestamp: action.completedAt,
            };
        }

        return {
            actionStatus: ACTION_STATUS.RESULT_ERROR,
            timestamp: action.completedAt,
        };
    }

    if (typeof action.result === 'boolean' || Array.isArray(action.result)) {
        return {
            actionStatus: getSpecificActionStatus(action.result),
            timestamp: action.completedAt,
        };
    }

    return { actionStatus: ACTION_STATUS.RESULT_UNKNOWN, timestamp: 0 };
};
export const getBooleanStatus = (result) => {
    if (result === true) return ACTION_STATUS.RESULT_SUCCESS;
    return ACTION_STATUS.RESULT_FAIL;
};
export const getRedeployStatus = (results) => {
    if (results === true) return ACTION_STATUS.RESULT_RE_DEPLOY_SUCCESS;
    return ACTION_STATUS.RESULT_RE_DEPLOY_FAIL;
};
export const getDryRunResult = getResultFactory('cli/local-node/data');
export const getRedeployDryRunStatus = (results) => {
    const result = getDryRunResult(results);
    if (result) return ACTION_STATUS.RESULT_RE_DEPLOY_DRY_RUN_DIFF;
    return ACTION_STATUS.RESULT_RE_DEPLOY_DRY_RUN_NO_DIFF;
};
const getInSyncResult = getResultFactory('in-sync');
export const getCheckInSyncStatus = (results) => {
    const result = getInSyncResult(results) || '';
    if (result === 'true') return ACTION_STATUS.RESULT_CHECK_SERVICE_IN_SYNC_IN_SYNC;
    if (result === 'false') return ACTION_STATUS.RESULT_CHECK_SERVICE_IN_SYNC_NOT_IN_SYNC;
    return ACTION_STATUS.RESULT_UNKNOWN;
};
export {
    selectCase as _selectCase,
    selectChoice as _selectChoice,
    selectContainer as _selectContainer,
    selectValue as _selectValue,
};
