import { pad } from './string';

// timestamp representing the milliseconds elapsed since the UNIX epoch.
export const timeStampToISOLocale = (timestamp) => {
    const date = new Date(timestamp);
    return `${date.getFullYear()
    }-${pad(date.getMonth() + 1)
    }-${pad(date.getDate())
    } ${pad(date.getHours())
    }:${pad(date.getMinutes())
    }:${pad(date.getSeconds())}`;
};

// timestamp representing the milliseconds elapsed since the UNIX epoch.
export const timeStampToSmallISOLocale = (timestamp) => {
    const date = new Date(timestamp);
    return `${date.getFullYear() - 2000
    }${pad(date.getMonth() + 1)
    }${pad(date.getDate())
    } ${pad(date.getHours())
    }:${pad(date.getMinutes())
    }`;
};

// diff in milliseconds
const timeDiffToString = (diff) => {
    if (diff < 60000) return 'just now';
    if (diff < 60 * 60000) return `${Math.floor(diff / 60000)} min ago`;
    if (diff < 24 * 60 * 60000) return `${Math.floor(diff / (60000 * 60))} hours ago`;
    if (diff < 365 * 24 * 60 * 60000) return `${Math.floor(diff / (60000 * 60 * 24))} days ago`;
    return `${Math.floor(diff / (365 * 24 * 60 * 60000))} years ago`;
};

export const ageToRelativeTimeString = ageInSeconds =>
    timeDiffToString(parseInt(ageInSeconds, 10) * 1000);

// timestamp representing the milliseconds elapsed since the UNIX epoch.
export const timeStampToRelativeTimeString = (timestamp) => {
    const diff = Date.now() - timestamp;
    return timeDiffToString(diff);
};

// timestamp representing the milliseconds elapsed since the UNIX epoch.
export const timeStampToDuration = (timestamp, now = Date.now()) => {
    const diff = now - timestamp;
    if (diff < 60000) return 'just logged in';
    if (diff < 2 * 60000) return '1 minute';
    if (diff < 60 * 60000) return `${Math.floor(diff / 60000)} minutes`;
    if (diff < 2 * 60 * 60000) return '1 hour';
    if (diff < 24 * 60 * 60000) return `${Math.floor(diff / (60000 * 60))} hours`;
    if (diff < 2 * 24 * 60 * 60000) return '1 day';
    return `${Math.floor(diff / (60000 * 60 * 24))} days`;
};

// return milliseconds elapsed since the UNIX epoch.
export const getTimestamp = () => Math.floor(Date.now());

export class Timer {
    constructor(ms) {
        this.ms = ms;
        this.timer = null;
        this.onTime = this.onTime.bind(this);
        this.callbacks = [];
        this.running = false;
        this.lastTime = undefined;
    }

    onTime() {
        const now = Date.now();
        const length = this.callbacks.length;
        for (let c = 0; c < length; c += 1) {
            this.callbacks[c](now, this.lastTime);
        }
        this.lastTime = now;
        if (this.running) {
            clearTimeout(this.timer);
            this.timer = setTimeout(this.onTime, this.ms);
        }
    }

    subscribe(cb) {
        if (!this.callbacks.includes(cb)) {
            this.callbacks.push(cb);
        }
    }

    unsubscribe(cb) {
        const index = this.callbacks.indexOf(cb);
        if (index !== -1) {
            this.callbacks.splice(index, 1);
        }
    }

    start() {
        clearTimeout(this.timer);
        this.running = true;
        this.timer = setTimeout(this.onTime, this.ms);
    }

    stop() {
        this.running = false;
        clearTimeout(this.timer);
        this.lastTime = undefined;
        this.timer = null;
    }
}

export const WAITER_DEFAULT_TIMEOUT = 100;
export const waiter = (callback, timeout = WAITER_DEFAULT_TIMEOUT) => (
    new Promise(resolve => setTimeout(() => resolve(callback()), timeout))
);
