export const isAction = item => item && item.kind === 'action';
export const isCase = item => item && item.kind === 'case';
export const isChoice = item => item && item.kind === 'choice';
export const isConfigFalse = item => item && item.config === false;
export const isLeaf = item => item && item.kind === 'leaf';
export const isList = item => item && item.kind === 'list';
export const isListEntry = item => item && item.kind === 'list-entry';
