import { omitKeys } from '@webui-one/core/utils/object';
import * as flagsActionTypes from 'modules/flags/actionTypes';

const initialState = {};
const emptyState = Object.freeze({});
export default function flags(state = initialState, action) {
    switch (action.type) {
    case flagsActionTypes.SET_FLAG:
        return {
            ...state,
            [action.flag]: action.value,
        };
    case flagsActionTypes.UNSET_FLAG:
        return omitKeys(state, [action.flag]);
    case flagsActionTypes.CLEAR_FLAGS:
        return emptyState;
    default:
        return state;
    }
}
