import { hasProp } from '@webui-one/core/utils/object';
import { flagIsSet } from './flags';

export const flagWrapper = (matches, defaultFunc) => (...args) => {
    const func = matches.reduce((acc, [flag, value, f]) => {
        if (flagIsSet(flag, value)) {
            return f;
        }
        return acc;
    }, null) || defaultFunc;
    return func(...args);
};

export const findMatchInState = (flags, props) =>
    Object.keys(flags).reduce((acc, key) => {
        if (acc) {
            return acc;
        }
        const prop = props[key] && props[key][flags[key]];
        return prop || acc;
    }, null);

export const replaceProps = (orgProps, newProps) => {
    // NOTE(dbildstr): it is OK for orgProps to be modified here, since we are
    // changing exported properties of a loaded module on the fly
    Object.keys(newProps).reduce((acc, name) => {
        acc[name] = newProps[name];
        return acc;
    }, orgProps);
    Object.keys(orgProps).reduce((acc, name) => {
        if (!hasProp(newProps, name)) {
            delete acc[name];
        }
        return acc;
    }, orgProps);
    return orgProps;
};

