import {
    findMatchInState,
    replaceProps,
} from 'modules/flags/utils';
import * as flagTypes from 'modules/flags/flagTypes';
import { hasProp } from '@webui-one/core/utils/object';
import { isDevEnv } from '@webui-one/core/utils/functional';

let flagStore;
const EmptyObject = Object.freeze({});
const callbacks = [];

const updateFlags = (state) => {
    callbacks.forEach((cb) => {
        cb(state);
    });
};
const resetCallbacks = () => { callbacks.splice(0); };
const addCallback = (cb) => { callbacks.push(cb); };

const createUpdater = (mod, props, defaultProps) => flagsState => (
    replaceProps(mod, findMatchInState(flagsState, props) || defaultProps)
);

const getStore = () => {
    if (isDevEnv() && flagStore) {
        return flagStore;
    }
    return { getState: () => EmptyObject };
};

const setStore = (store) => { flagStore = store; };

const flagIsSet = (flag, value = true) => {
    const { flags } = getStore().getState();
    return isDevEnv() &&
        hasProp(flags, flag) &&
        value === flags[flag];
};

const availableFlags = [
    {
        name: flagTypes.REDUX_LOG_DISABLE,
        title: 'Disable Redux Log',
        values: ['true', 'false'],
    },
    {
        name: flagTypes.USER_TIMING_ENABLE,
        title: 'Enable User Timing Middleware',
        values: ['true', 'false'],
    },
];

const getAvailableFlags = () => availableFlags;

export {
    addCallback,
    createUpdater,
    getAvailableFlags,
    getStore,
    findMatchInState,
    flagIsSet,
    replaceProps,
    resetCallbacks,
    setStore,
    updateFlags,
};
