import { isFunction, pipe } from '../utils/functional';
import { init, isNonEmptyArray, last, nth as coreNth, reverse } from '../utils/array';

import * as KPI from './kindpathItem';

const filter = predicate => array => array.filter(predicate);
const nth = offest => array => coreNth(offest, array);

const tap = func => (item) => {
    if (isFunction(func)) func(item);
    return item;
};

// eslint-disable-next-line no-console, no-unused-vars
const createLogger = (log = console.log) => tap(i => log('TAP', i));

export const isActionDescendant = kp => pipe(init, filter(KPI.isAction), isNonEmptyArray)(kp);
export const isCase = kp => pipe(last, KPI.isCase)(kp);
export const isCaseChild = kp => pipe(nth(-2), KPI.isCase)(kp);
export const isCaseDescendant = kp => pipe(init, filter(KPI.isCase), isNonEmptyArray)(kp);
export const isConfigFalse = kp => pipe(last, KPI.isConfigFalse)(kp);
export const isLeaf = kp => pipe(last, KPI.isLeaf)(kp);
export const isList = kp => pipe(last, KPI.isList)(kp);
export const isListDescendant = kp => pipe(init, filter(KPI.isList), isNonEmptyArray)(kp);

const internalToKeypath = ({ kindpath, isRelative }) => kindpath.reduce((acc, cur, ix) => {
    const { name, rootPath } = cur;

    if (KPI.isChoice(cur)) return acc;
    if (KPI.isCase(cur)) return acc;

    if (rootPath) return rootPath;
    if (isRelative && ix === 0) return name;

    return `${acc}/${name}`;
}, '');

export const toKeypath = kindpath => internalToKeypath({ kindpath, isRelative: false });
export const toRelativeKeypath = kindpath => internalToKeypath({ kindpath, isRelative: true });

const internalToChoiceCasePath = ({ kindpath, isRelative }) => kindpath.reduce((acc, cur, ix) => {
    const { name, rootPath } = cur;

    if (rootPath) return rootPath;
    if (isRelative && ix === 0) return name;

    return `${acc}/${name}`;
}, '');

export const toChoiceCasePath = kindpath => (
    internalToChoiceCasePath({ kindpath, isRelative: false })
);

export const toRelativeChoiceCasePath = kindpath => (
    internalToChoiceCasePath({ kindpath, isRelative: true })
);

const keyRE = /\{[^}]+\}/;

export const toKeylessKindpath = kindpath => kindpath.map((ki) => {
    const { name, rootPath, ...item } = ki;
    if (rootPath) {
        item.rootPath = rootPath.replace(keyRE, '{}');
    }
    item.name = name.replace(keyRE, '{}');
    return item;
});

export const getRootKindpath = (kindpath) => {
    if (kindpath && kindpath[0].rootPath) return kindpath.slice(0, 1);
    const ix = kindpath.findIndex(item => item.kind === 'list-entry');
    return kindpath.slice(0, ix + 1);
};

export const getRootKeypath = kindpath => toKeypath(getRootKindpath(kindpath));

export const getRelativeKindpath = (kindpath) => {
    const rootKindpath = getRootKindpath(kindpath);
    return kindpath.slice(rootKindpath.length);
};

export const getRelativeKeypath = kindpath => toRelativeKeypath(getRelativeKindpath(kindpath));

export const getParentCase = kindpath => reverse(kindpath).reduce((acc, cur, ix, arr) => {
    if (acc !== undefined) return acc;
    if (KPI.isCase(cur)) return reverse(arr.slice(ix));
    return undefined;
}, undefined);

export const getCaseAncestors = kindpath => reverse(kindpath).reduce((acc, cur, ix, arr) => {
    if (KPI.isCase(cur)) {
        const cse = reverse(arr.slice(ix));
        const rest = reverse(arr.slice(0, ix));
        acc.push({
            case: cse,
            relativeToCase: rest,
        });
    }
    return acc;
}, []);

export const getChoiceCaseSymlinks = (kindpath) => {
    if (!isCaseDescendant(kindpath)) return [];

    const caseAncestors = getCaseAncestors(kindpath);

    return caseAncestors.map(ca => ({
        path: `${toKeypath(ca.case)}/${toRelativeChoiceCasePath(ca.relativeToCase)}`,
        symlink: `${toChoiceCasePath(ca.case)}/${toRelativeChoiceCasePath(ca.relativeToCase)}`,
    }));
};

const deviceConfigChildRE = new RegExp('^/ncs:devices/device{[^}]+}/(config/([^:]+?):.*)$');
export const isDeviceConfig = kp => toKeypath(kp).match(deviceConfigChildRE) !== null;

const deviceConfigRE = new RegExp('^/ncs:devices/device{[^}]+}/config$');
const isNcsDevicesDeviceConfig = kp => toKeypath(kp).match(deviceConfigRE) !== null;

const isNcsDevices = kp => toKeypath(kp) === '/ncs:devices';

const deviceRE = new RegExp('^/ncs:devices/device$');
const isNcsDevicesDevice = kp => toKeypath(kp).match(deviceRE) !== null;

const deviceListEntryRE = new RegExp('^/ncs:devices/device{[^}]*}$');
const isNcsDevicesDeviceListEntry = kp => toKeypath(kp).match(deviceListEntryRE) !== null;

const isDeviceConfigAncestor = (kp) => {
    if (isNcsDevices(kp)) return true;
    if (isNcsDevicesDevice(kp)) return true;
    if (isNcsDevicesDeviceListEntry(kp)) return true;
    if (isNcsDevicesDeviceConfig(kp)) return true;
    return false;
};

export const toNedKindpath = kindpath => kindpath.reduce((acc, cur, ix, arr) => {
    if (ix === 0) {
        const { rootPath = '' } = cur;
        const match = rootPath.match(deviceConfigChildRE);
        if (match !== null) {
            acc.push({
                ...cur,
                rootPath: `/ned-id:${match[2]}/${match[1]}`,
                nedId: true,
            });
            return acc;
        }
    }
    const curKindpath = arr.slice(0, ix + 1);

    if (isDeviceConfig(kindpath) && isDeviceConfigAncestor(curKindpath)) return acc;

    if (isDeviceConfig(curKindpath)) {
        const parent = arr.slice(0, ix);

        if (isNcsDevicesDeviceConfig(parent)) {
            const { name } = cur;
            const prefix = name.split(':')[0];
            acc.push({
                nedId: true,
                name: 'config',
                kind: 'container',
                rootPath: `/ned-id:${prefix}/config`,
            });
            acc.push(cur);
            return acc;
        }

        acc.push(cur);
        return acc;
    }

    acc.push(cur);

    return acc;
}, []);

export {
    createLogger as _createLogger,
    isDeviceConfigAncestor as _isDeviceConfigAncestor,
    filter as _filter,
    nth as _nth,
    tap as _tap,
};
