export const METADATA_TAG = 'userdata';

export const userProfilePath = user => (
    `/webui:webui/data-stores/user-profile{${user}}`
);
export const userProfileDataPath = (user, key) => (
    `${userProfilePath(user)}/profile{${key}}`
);
export const userProfileMetaDataPath = user => (
    `${userProfileDataPath(user, METADATA_TAG)}`
);
export const userdataPath = (user, key) => (
    `${userProfileDataPath(user, key)}/value`
);

export const userMetadataPath = user => userdataPath(user, METADATA_TAG);

export const subscribePath = user => (
    `${userProfilePath(user)}/profile`
);
