import { createLogger } from 'redux-logger';

import * as flagTypes from 'modules/flags/flagTypes';

import jsonrpc from '@webui-one/core/jsonrpc';
import {
    clearFlags,
    setFlag,
    unsetFlag,
} from 'modules/flags/actions';
import { flagIsSet, flagMiddleware } from 'modules/flags';

import { isDevEnv } from '@webui-one/core/utils/functional';

import userTiming from 'core/userTimingMiddleware';

export const exposeDevCode = (store) => {
    if (!isDevEnv()) {
        return;
    }

    window._store = store; // eslint-disable-line no-underscore-dangle

    window._getActiveFlags = () => ( // eslint-disable-line no-underscore-dangle
        { ...store.getState().flags }
    );

    window._clearFlags = () => { // eslint-disable-line no-underscore-dangle
        store.dispatch(clearFlags());
    };
    window._unsetFlag = (flag, value) => { // eslint-disable-line no-underscore-dangle
        store.dispatch(unsetFlag(flag, value));
    };
    window._setFlag = (flag, value) => { // eslint-disable-line no-underscore-dangle
        store.dispatch(setFlag(flag, value));
    };
    window._jsonrpc = jsonrpc; // eslint-disable-line no-underscore-dangle
};

export const initDevModeLogger = (
    middlewares,
    composeMethod,
    {
        createReduxLogger = createLogger,
        logger = console, // eslint-disable-line no-console
    } = {},
) => {
    if (!isDevEnv()) {
        return [
            middlewares,
            composeMethod,
        ];
    }

    const reduxLogger = createReduxLogger({
        collapsed: true,
        logger,
        predicate: () => !flagIsSet(flagTypes.REDUX_LOG_DISABLE, 'true'),
    });
    return [
        [flagMiddleware, ...middlewares, reduxLogger, userTiming],
        window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || composeMethod, // eslint-disable-line
    ];
};
