/* eslint-disable import/prefer-default-export */

const nodeAttribs = ['columnsVisibility'];
export const nodesFilter = ({ state }) => (
    Object.keys(state).reduce((nodes, keypath) => {
        const node = state[keypath];
        const newNode = {};

        nodeAttribs.forEach((attr) => {
            if (attr in node) {
                newNode[attr] = node[attr];
            }
        });
        if (Object.keys(newNode).length > 0) {
            Object.assign(nodes, {
                [keypath]: newNode,
            });
        }
        return nodes;
    }, {})
);

