import { isNonEmptyArray } from '@webui-one/core/utils/array';
import { isNonEmpty, isObject } from '@webui-one/core/utils/object';
import { isString } from '@webui-one/core/utils/string';
import { isNumber } from '@webui-one/core/utils/number';
import {
    emptyFilter,
} from './storage-filters';
import { isFunction } from '@webui-one/core/utils/functional';

const DEFAULT_STORAGE_TAG = 'default_data_storage';
let storageTag = DEFAULT_STORAGE_TAG;

const getStorageTag = () => storageTag;
const setStorageTag = (tag) => { storageTag = tag; };

const getStateFromStorage = (tag) => {
    try {
        return JSON.parse(window.localStorage.getItem(tag) || '{}');
    } catch (e) {
        return {};
    }
};

const loadMetadata = () => getStateFromStorage(storageTag);

const writeState = (filteredState) => {
    window.localStorage.setItem(storageTag, JSON.stringify(filteredState));
};

const hasData = value => (
    (isObject(value) && isNonEmpty(value))
    || isNonEmptyArray(value)
    || isString(value)
    || isNumber(value)
);

const findItem = (items, filter) => items.find(filter);

const filterState = (state, { filters, defaultFilter, options } = {}) => (
    Object.keys(state).reduce((newState, key) => {
        const filterFunction = findItem(
            [
                filters && filters[key],
                defaultFilter,
                emptyFilter,
            ],
            f => isFunction(f),
        );
        const value = filterFunction({ state: state[key], key, options });
        if (hasData(value)) {
            return {
                ...newState,
                [key]: value,
            };
        }
        return newState;
    }, {})
);

const deleteState = tag => window.localStorage.removeItem(tag);
const updateStoredState = (
    state,
    updateFilters,
    {
        deleteStateFunc = deleteState,
        filterStateFunc = filterState,
        hasStorableData = hasData,
        writeStateFunc = writeState,
    } = {},
) => {
    const filteredState = filterStateFunc(state, updateFilters);
    if (hasStorableData(filteredState)) {
        writeStateFunc(filteredState);
    } else {
        deleteStateFunc(storageTag);
    }
};

const unloadHandler = (getState, updateHandler) => () => {
    const currentState = getState();
    const updateFilters = isFunction(updateHandler)
        ? updateHandler(currentState)
        : {};
    updateStoredState(currentState, updateFilters);
};

const initStorage = (
    getState,
    updateHandler,
    name = DEFAULT_STORAGE_TAG,
    {
        handler = unloadHandler,
        setTag = setStorageTag,
    } = {},
) => {
    setTag(name);
    window.addEventListener(
        'beforeunload',
        handler(getState, updateHandler),
    );
};

const loadStoredState = (initialState) => {
    const meta = loadMetadata();
    const state = initialState || {};
    return { ...state, ...meta };
};

export {
    DEFAULT_STORAGE_TAG as _DEFAULT_STORAGE_TAG,
    filterState as _filterState,
    getStateFromStorage as _getStateFromStorage,
    getStorageTag,
    hasData,
    initStorage,
    loadMetadata,
    loadStoredState,
    setStorageTag,
    updateStoredState,
    writeState as _writeState,
};
