// => {
//      items: [{ name: 'foo', kind: 'leaf' }, { name: 'bar', kind: 'key' }],
//      rest: [{ name: 'baz', kind: 'container' }],
// }
export const groupBy = keyFn => obj => (
    Object.keys(obj).reduce((acc, cur) => {
        const key = keyFn(obj[cur]);

        if (key in acc) {
            return { ...acc, [key]: acc[key].concat(obj[cur]) };
        }
        return { ...acc, [key]: [obj[cur]] };
    }, {})
);

export const isObject = item => (
    item != null && typeof item === 'object' && Array.isArray(item) === false
);

export const isEmpty = (o) => {
    if (!isObject(o)) return true;
    if (Object.keys(o).length === 0) return true;
    return false;
};

export const isNonEmpty = (o) => {
    if (!isObject(o)) return false;
    return Object.keys(o).length > 0;
};

export const isShallowEqual = (a, b) => {
    if (a === b) return true;
    if (a === undefined || b === undefined) return false;

    const ak = Object.keys(a);
    const bk = Object.keys(b);

    return ak.length === bk.length && ak.every(k => a[k] === b[k]);
};

export const omitKeys = (obj = {}, keys) => {
    const excludeKeys = Array.isArray(keys) ? keys : [];
    return Object.keys(obj).reduce((acc, key) => {
        if (excludeKeys.indexOf(key) === -1) {
            acc[key] = obj[key];
        }
        return acc;
    }, {});
};

export const omitKeysByFilter = (obj = {}, filterFunc) => (
    Object.keys(obj).reduce((acc, key) => {
        if (filterFunc(key) === false) {
            acc[key] = obj[key];
        }
        return acc;
    }, {})
);

export const hasProp = (obj, key) => (
    obj && Object.prototype.hasOwnProperty.call(obj, key)
);

export const isDate = d => d instanceof Date;

export const isReactOwnerProp = (key, obj) =>
    key === '_owner' && obj.$$typeof;

export const isRegExp = r => r instanceof RegExp;

export const isDeepEqual = (a, b) => {
    if (Object.is(a, b)) return true;

    if (Array.isArray(a) && Array.isArray(b)) {
        if (a.length !== b.length) return false;
        for (let i = 0; i < a.length; i += 1) {
            if (!isDeepEqual(a[i], b[i])) return false;
        }
        return true;
    }

    if (!isObject(a) || !isObject(b)) {
        return false;
    }

    const regA = isRegExp(a);
    const regB = isRegExp(b);
    if (regA !== regB) return false;
    if (regA && regB) return a.toString() === b.toString();

    const dateA = isDate(a);
    const dateB = isDate(b);
    if (dateA !== dateB) return false;
    if (dateA && dateB) return a.getTime() === b.getTime();

    const keys = Object.keys(a);
    if (keys.length !== Object.keys(b).length) return false;
    for (let k = 0; k < keys.length; k += 1) {
        const key = keys[k];
        if (!hasProp(b, key)) return false;
        if (!isReactOwnerProp(key, a) && !isDeepEqual(a[key], b[key])) return false;
    }
    return true;
};
/* eslint-disable no-param-reassign */
const mutateExtend = (target, obj) => {
    if (!(isObject(obj))) return target;
    Object.keys(obj).forEach((key) => {
        const val = obj[key];
        if (isObject(val)) {
            const targetObj = isObject(target[key]) ? target[key] : {};
            // eslint-disable-next-line no-use-before-define
            target[key] = assignDeep(targetObj, val);
        } else {
            target[key] = val;
        }
    });
    return target;
};
/* eslint-enable no-param-reassign */

export const assignDeep = (...objs) => {
    if (objs.length === 0) return undefined;
    const [target, ...rest] = objs;
    if (!(isObject(target))) throw new Error('First argument has to be an object');
    const sources = compact(rest);
    if (sources.length === 0) return target;
    sources.forEach(obj => mutateExtend(target, obj));
    return target;
};

const isConsideredEmpty = data =>
    data === undefined
    || data === null
    || isEmptyArray(data)
    || (isObject(data) && isEmpty(data));

export const stringifyObject = (data, seperator = '. ') => {
    if (isConsideredEmpty(data)) return '';
    if (isObject(data)) {
        return Object.entries(data)
            .map(([key, value]) => `${key}: ${stringifyObject(value)}`)
            .join(seperator);
    }
    if (Array.isArray(data)) return data.map(item => stringifyObject(item)).join(seperator);
    return `${data}`;
};

export const ensureObject = object => (
    isObject(object) ? object : {}
);

export const objectHasAllProps = (object, props) => {
    // eslint-disable-next-line no-param-reassign
    object = ensureObject(object);
    // eslint-disable-next-line no-param-reassign
    props = ensureObject(props);
    return Object.keys(props).every(key => (
        object[key] === props[key]
    ));
};

export const path = (pathArray, obj) => pathArray.reduce((acc, cur, ix) => {
    if (acc === undefined) return undefined;
    if (!isObject(acc) && ix === pathArray.length - 1) return undefined;
    return acc[cur];
}, obj);

export const pathOr = (defaultValue, pathArray, obj) => (
    defaultTo(defaultValue, path(pathArray, obj))
);
