import { itemTypes } from '../schemas';
import { getName } from '../utils';

const createAccessRuleName = (access) => {
    if (access === undefined) return undefined; // choice and case are missing access rules
    const { create, read, update, delete: del, execute } = access;
    let name = '';
    if (create) name += 'c';
    if (read) name += 'r';
    if (update) name += 'u';
    if (del) name += 'd';
    if (execute) name += 'e';
    return name;
};

const createEvaluatedPath = parent => (schema) => {
    if (parent.isRoot) {
        return parent.path;
    }
    const name = getName(parent)(schema);

    return `${parent.path}/${name}`;
};

const createEvaluatedData = (schema) => {
    /* eslint-disable camelcase */
    const { access: rawAccess, evaluated_when_entry, kind, is_action_output } = schema.data;
    const data = { access: createAccessRuleName(rawAccess) };

    if (kind !== undefined) data.kind = kind;
    if (evaluated_when_entry !== undefined) data.evaluated_when_entry = evaluated_when_entry;
    if (is_action_output !== undefined) data.is_action_output = is_action_output;
    /* eslint-enable camelcase */
    return data;
};

const isKeyless = (path) => {
    if (path.endsWith('{}')) return true;
    if (path.includes('{}/')) return true;
    return false;
};

export const createEvaluatedSchema = parent => (rawSchema) => {
    const {
        hasEvaluatedWhenEntries,
        relativeKeypath,
        kindpath,
    } = rawSchema;

    const path = createEvaluatedPath(parent)(rawSchema);
    const data = createEvaluatedData(rawSchema);
    const itemType = isKeyless(path) ?
        itemTypes.EVALUATED_KEYLESS_SCHEMA : itemTypes.EVALUATED_SCHEMA;

    const schema = { path, data, itemType };

    if (kindpath) schema.kindpath = kindpath;

    if (relativeKeypath !== undefined) schema.relativeKeypath = relativeKeypath;

    if (rawSchema.data.kind === 'case' && hasEvaluatedWhenEntries !== undefined) {
        schema.hasEvaluatedWhenEntries = hasEvaluatedWhenEntries;
    }

    return schema;
};

export {
    createAccessRuleName as _createAccessRuleName,
    createEvaluatedData as _createEvaluatedData,
    createEvaluatedPath as _createEvaluatedPath,
};

