import {
    createSelector,
    createSelectorCreator,
    defaultMemoize,
} from 'reselect';
import {
    intersect,
} from '@webui-one/core/utils/array';
import {
    isEmpty,
    isShallowEqual,
    pathOr,
} from '@webui-one/core/utils/object';
import {
    getActionStatus,
    getCheckSyncStatus,
    getConnectionStatus,
    getPingStatus,
    getSyncFromStatus,
    selectYangActions,
    getCompareConfigStatus,
} from 'modules/yangActions/selectors';
import {
    selectNode,
    selectNodes,
    selectOffset,
    selectTotalNumberOfResults,
    selectsortOrder,
    selectSortColumn,
} from 'modules/nodes/selectors';
import {
    createColumnsVisibilitySelector,
} from 'modules/userdata/selectors';

import { selectSchema } from 'modules/schemas/reducer';
import {
    ALARM_PRECEDENCE,
    ALARM_STATUS,
    COLUMNS_SELECTION,
    DEVICE_ACTIONS,
    DEVICE_MANAGER_USER_STORAGE_PATH,
    alarmsPath,
    deviceListPath,
    deviceTypeSubPath,
    devicesRootPath,
    deviveGroupListPath,
    getDeviceGroupDeviceListDevicePath,
    getDeviceGroupDeviceListPath,
    getDeviceListKeyPath,
} from './paths';

const EMPTY_OBJECT = Object.freeze({});
export const labelTypes = Object.freeze({
    LBL_INCLUDE: 'LBL_INCLUDE',
    LBL_EXCLUDE: 'LBL_EXCLUDE',
    LBL_ONE_OF: 'LBL_ONE_OF',
    SEARCH: 'SEARCH',
});

const userStorageProps = {
    path: DEVICE_MANAGER_USER_STORAGE_PATH,
};

const deviceColumnSelector = createColumnsVisibilitySelector();
export const selectDeviceColumns = state => deviceColumnSelector(state, userStorageProps);

export const selectShowAddDeviceModal = state => state.devices.showAddDeviceModal;

export const deviceRootSelector = state => selectNode(state, deviceListPath);

export const selectDeviceOffset = state => selectOffset(state, deviceListPath);

export const selectDeviceSortOrder = state => selectsortOrder(state, deviceListPath);
export const selectDeviceSortColumn = state => selectSortColumn(state, deviceListPath);


export const deviceListSelector = createSelector(
    deviceRootSelector,
    state => selectNodes(state),
    (deviceRoot, nodes) => {
        const rows = deviceRoot.rows || [];
        const filteredRows = (rows).filter((row) => {
            const node = nodes[getDeviceListKeyPath(row.name)];
            return !node || node.isDeleted !== true;
        });

        return filteredRows.map((row) => {
            const node = nodes[getDeviceListKeyPath(row.name)];
            const isDirty = !!node && (node.isDirty || node.dirtyRefCount > 0);
            return { ...row, isDirty };
        });
    },
);

export const deviceListKeys = createSelector(
    deviceListSelector,
    deviceList => deviceList.map(d => getDeviceListKeyPath(d.name)),
);

export const selectDeviceManager = state => state.devices;
export const selectSelectedDevices = state => selectDeviceManager(state).devices || EMPTY_OBJECT;


/* GROUP / Other stuff */
const getGroupValues = (name, nodes) => {
    const node = nodes[getDeviceGroupDeviceListPath(name)];
    if (!node || node.isLoading || !node.rows) return undefined;
    return node.rows.reduce((acc, o) =>
        [...acc, ...Object.values(o).filter((d) => {
            const n = nodes[getDeviceGroupDeviceListDevicePath(name, d)] || {};
            return n.isDeleted !== true;
        })], []);
};

export const selectDeviceGroupChangeIsPending = state => state.devices.deviceGroupChangeIsPending;

export const selectDeviceGroups = state => selectNode(state, deviveGroupListPath);


const cmpGroups = (a, b) =>
    (a && b) && ((isEmpty(a) && isEmpty(b)) || (Object.keys(a).every(k => a[k] === b[k])));

export const selectDeviceGroupsWithDevices =
    createSelectorCreator(defaultMemoize, cmpGroups)(createSelector(
        selectDeviceGroups,
        state => selectNodes(state),
        (groups, nodes) => {
            if (!groups.rows) return undefined;
            return groups.rows.reduce((acc, g) => ({
                ...acc,
                [g.name]: getGroupValues(g.name, nodes) || [],
            }), {});
        },
    ), groups => groups);

export const selectLabels = state => state.devices.labels;

const labelToXpath = l => `name=../device-group[name="${l.name}"]/device-name`;

export const selectLabelsXpathExpression = createSelector(
    selectLabels,
    (lbls) => {
        const arr = Object.values(lbls);
        const inclu = arr.filter(l => l.type === labelTypes.LBL_INCLUDE).map(labelToXpath).join(' and ');
        const oneof = arr.filter(l => l.type === labelTypes.LBL_ONE_OF).map(labelToXpath).join(' or ');
        const exclu = arr.filter(l => l.type === labelTypes.LBL_EXCLUDE).map(lbl => `not(${labelToXpath(lbl)})`).join(' and ');
        return [inclu, oneof, exclu].filter(s => s.length).map(s => `(${s})`).join(' and ');
    },
);

export const selectHasActiveLabels = createSelector(
    selectLabels,
    lbls => Object.keys(lbls).length > 0,
);


export const selectDeviceSearch = state =>
    pathOr('', ['devices', 'labels', labelTypes.SEARCH, 'name'], state);

const searchToXpathFactory = value => column => `contains(${column},"${value}")`;

const SEARCH_COLUMNS = COLUMNS_SELECTION.slice(0, 4);
const selectDeviceSearchXpath = createSelector(
    selectDeviceSearch,
    (search) => {
        if (search !== '') {
            const searchToXpath = searchToXpathFactory(search);
            return SEARCH_COLUMNS.map(col => searchToXpath(col.key)).join(' or ');
        }
        return '';
    });

export const selectDeviceXpathExpression = createSelector(
    selectLabelsXpathExpression,
    selectDeviceSearchXpath,
    (lbsExpr, searchExpr) => {
        const exprs = [searchExpr, lbsExpr].filter(s => !!s).map(s => `(${s})`).join(' and ');
        if (exprs.length > 0) return `device[${exprs}]`;
        return 'device';
    });

export const loadStatus = {
    ERROR: 'ERROR',
    LOADING: 'LOADING',
    LOADED: 'LOADED',
    NOT_LOADED: 'NOT_LOADED',
};

export const selectLoadStatus = createSelector(
    state => selectNode(state, deviveGroupListPath).isLoading,
    (isLoading) => {
        if (isLoading === undefined) return loadStatus.NOT_LOADED;
        if (isLoading) return loadStatus.LOADING;
        return loadStatus.LOADED;
    },
);

export const filteredDeviceCountSelector = createSelector(
    deviceListSelector,
    deviceList => deviceList.length,
);

const shouldUpdateDeviceAction = (da, action, yangAction) => (
    !da || !da[action] || da[action].completedAt < yangAction.completedAt
);

const updateDeviceAction = (actionResults, devicePath, action, yangAction) => {
    const result = actionResults;
    if (!(devicePath in result)) {
        result[devicePath] = {};
    }
    result[devicePath][action] = yangAction;
    return result;
};

export const selectDevicesYangActions = createSelectorCreator(defaultMemoize, isShallowEqual)(
    deviceListKeys,
    selectYangActions,
    (getDeviceListKeyPaths, yangActions) => {
        const actionResults = getDeviceListKeyPaths.reduce((acc, key) => ({
            ...acc,
            [key]: DEVICE_ACTIONS.reduce((accAct, action) => ({
                ...accAct,
                [action]: yangActions[`${key}/${action}`],
            }), {}),
        }), {});
        return actionResults;
    });

export const selectExpandedRows = state => selectDeviceManager(state).expandedRows || EMPTY_OBJECT;
export const selectExpandedRow = (state, path) =>
    selectExpandedRows(state)[path] || EMPTY_OBJECT;
export const isExpanded = expandState => !!(expandState || {}).isExpanded;
export const getExpandedColumnName = expandState => (expandState || {}).column;

export const selectIsExpanded = (state, path) => isExpanded(selectExpandedRows(state, path));

const actionHandlers = {
    ping: getPingStatus,
    connect: getConnectionStatus,
    'check-sync': getCheckSyncStatus,
    'compare-config': getCompareConfigStatus,
    'sync-from': getSyncFromStatus,
    'sync-to': getSyncFromStatus,
};

const getActionResultHandler = actionKey =>
    actionHandlers[actionKey] || getSyncFromStatus;

const getActionsWithStatus = actions =>
    Object.keys(actions).reduce((acc, actionKey) => ({
        ...acc,
        [actionKey]: {
            status: getActionStatus(
                actions,
                actionKey,
                getActionResultHandler(actionKey)).actionStatus,
            yangAction: actions[actionKey],
        },
    }), {});

const alarmSeverity = device =>
    ALARM_PRECEDENCE.reduceRight((acc, a) =>
        (device[a] > 0 ? a.replace('./alarm-summary/', '') : acc), ALARM_STATUS.CLEARED);

export const deviceRowSelector = createSelector(
    deviceListSelector,
    selectDevicesYangActions,
    selectSelectedDevices,
    selectExpandedRows,
    (devicesList, yangActions, selected, expandedRows) => devicesList.map((device) => {
        const rowKey = getDeviceListKeyPath(device.name);
        const expandedColumn = getExpandedColumnName(expandedRows[rowKey]);
        const actions = getActionsWithStatus(yangActions[rowKey]);
        if (expandedColumn) {
            actions[expandedColumn].isExpanded = isExpanded(expandedRows[rowKey]);
        }
        const metadata = selected[rowKey] || {};
        const alarm = alarmSeverity(device);
        const yangAction = {
            ...(yangActions[rowKey] || {})[expandedColumn],
            actionStatus: actions[expandedColumn],
        };
        return {
            data: {
                ...device,
                ...metadata,
                ...actions,
                alarm,
                config: device.name,
                expandable: {
                    isExpanded: isExpanded(expandedRows[rowKey]),
                    column: expandedColumn,
                    yangAction,
                },
            },
            rowKey,
        };
    }),
);


export const deviceListIsLoading = (state) => {
    if (!(deviceListPath in state.nodes)) {
        return true;
    }

    return state.nodes[deviceListPath].isLoading;
};

export const selectedDeviceRowsSelector = createSelector(
    deviceRowSelector,
    rows => rows.filter(r => r.data.isSelected),
);

export const selectedDeviceCountSelector = createSelector(
    selectedDeviceRowsSelector,
    rows => rows.length,
);

export const selectTotalNumberOfDevices = createSelector(
    filteredDeviceCountSelector,
    deviceRootSelector,
    state => selectTotalNumberOfResults(state, deviceListPath),
    (count, root, total) => total - ((root.rows || []).length - count),
);

export const selectDeviceGroupsByDeviceUnion = createSelector(
    selectedDeviceRowsSelector,
    selectDeviceGroupsWithDevices,
    (devicesList, groups) => {
        if (!groups) return [];
        return Object.keys(groups).reduce((acc, cur) => {
            const deviceNamesInGroup = groups[cur];
            if (!deviceNamesInGroup) return acc;
            const deviceNames = devicesList.map(d => d.data.name);
            const intersection = intersect(deviceNames, deviceNamesInGroup);
            if (intersection.length > 0) {
                acc.push(cur);
            }
            return acc;
        }, []);
    },
);

const deviceRE = /^(\/ncs:devices\/device\{([^}]+)\}).*/;

export const selectModule = (state, path) => {
    const match = deviceRE.exec(path);
    if (!match) return undefined;
    const modulePath = `${match[1]}/module`;

    return state.devices.modules[modulePath];
};

export const selectModuleIsLoading = (state, path) => {
    const module = selectModule(state, path);
    return !!(module && module.isLoading);
};

export const countDevicesByType = createSelector(
    deviceListSelector,
    (deviceList) => {
        const typeCount = deviceList.reduce((acc, device) => {
            const type = device[deviceTypeSubPath];
            if (type) {
                acc[type] = 1 + (acc[type] || 0);
            }
            return acc;
        }, {});
        return Object.keys(typeCount).map(type => ({ name: type, value: typeCount[type] }))
            .sort((a, b) => a.name.localeCompare(b.name));
    },
);

export const selectDeviceAlarms = state => selectNode(state, alarmsPath);

export const selectDeviceActions = createSelector(
    state => selectSchema(state, devicesRootPath),
    schema => DEVICE_ACTIONS.filter(a => schema &&
            schema.children &&
            schema.children.find(c => c.name === a) !== undefined,
    ),
);

export {
    alarmSeverity as _alarmSeverity,
    labelToXpath as _labelToXpath,
    shouldUpdateDeviceAction as _shouldUpdateDeviceAction,
    getActionResultHandler as _getActionResultHandler,
    updateDeviceAction as _updateDeviceAction,
};
