import {
    pathWithoutKeys,
    pathWithoutKeyInstances,
} from '@webui-one/core/keypath/kp-parser';

import * as KIP from '@webui-one/core/kindpath';

import {
    isCase,
    isChoice,
    isList,
    isListEntry,
    isAccessDenied,
} from '@webui-one/core/schema';

import { pipe } from '@webui-one/core/utils/functional';
import { flatten } from '@webui-one/core/utils/array';
import { assignDeep } from '@webui-one/core/utils/object';

import { createParser } from './parser/parser';

import {
    getListPaths,
    getPrefixFromPath,
    parsePath,
    parsePathByItemType,
} from './utils';

export const itemTypes = {
    STATIC_SCHEMA: 'STATIC_SCHEMA',
    EVALUATED_SCHEMA: 'EVALUATED_SCHEMA',
    EVALUATED_KEYLESS_SCHEMA: 'EVALUATED_KEYLESS_SCHEMA',
    VALUE: 'VALUE',
    STATIC_SYMLINK: 'STATIC_SYMLINK',
    EVALUATED_SYMLINK: 'EVALUATED_SYMLINK',
};

const getValues = (parentPath, schemas) => (
    schemas.reduce((acc, cur) => {
        const { itemType, path, ...rest } = cur;
        if (itemType !== itemTypes.VALUE) return acc;

        if (path === parentPath) {
            acc[parentPath] = { ...rest, ...acc[parentPath] };
            return acc;
        }

        const itemPath = path.replace(new RegExp(`^${parentPath}/`), '');
        acc[parentPath].values[itemPath] = rest;
        return acc;
    }, { [parentPath]: { values: {} } })
);

const getSourcePath = (rootSchemaPath, schemaPath) => {
    const rootKeylessPath = pathWithoutKeys(rootSchemaPath);
    return schemaPath.replace(rootKeylessPath, rootSchemaPath);
};


const getDependencies = schemasList => schemasList.reduce((acc, cur) => {
    const { path, itemType, ...schema } = cur;

    if (schema.data === undefined) return acc;

    if (schema.data.kind === 'list') return acc;

    if (schema.data.deps) {
        schema.data.deps.forEach((d) => {
            const { rootSchemaPath } = parsePath(path);
            const src = getSourcePath(rootSchemaPath, d);

            if (!(src in acc)) {
                acc[src] = { dependants: [path] };
            } else {
                acc[src] = {
                    ...acc[src],
                    dependants: [
                        ...acc[src].dependants || [],
                        path,
                    ],
                };
            }
        });
    }
    return acc;
}, {});

const appendDependencies = (
    schemasList,
    {
        funcs: {
            getDependenciesFunc = getDependencies,
        } = {},
    } = {},
) => {
    const dependencies = getDependenciesFunc(schemasList);
    const depPaths = Object.keys(dependencies);

    return schemasList.reduce((acc, cur) => {
        if (cur.itemType === itemTypes.STATIC_SCHEMA && depPaths.includes(cur.path)) {
            acc.push({
                ...cur,
                dependants: [...dependencies[cur.path].dependants],
            });
        } else if (cur.itemType === itemTypes.STATIC_SYMLINK && depPaths.includes(cur.path)) {
            acc.push({
                ...cur,
                dependants: [...dependencies[cur.path].dependants],
            });
        } else {
            acc.push(cur);
        }
        return acc;
    }, []);
};

const appendListSymlinks = schemasList => schemasList.reduce((acc, cur) => {
    if (cur.data.kind === 'list' && cur.relativeKeypath === undefined) {
        const { listPath, listEntryPath } = getListPaths(cur);

        acc.push({
            ...cur,
            data: {
                ...cur.data,
                kind: 'list',
            },
            path: listPath,
        });
        acc.push({
            ...cur,
            data: {
                ...cur.data,
                kind: 'list-entry',
            },
            path: listEntryPath,
        });
        return acc;
    }
    acc.push(cur);
    return acc;
}, []);

const getSymlinkItemType = schema => (
    schema.itemType === itemTypes.STATIC_SCHEMA ?
        itemTypes.STATIC_SYMLINK : itemTypes.EVALUATED_SYMLINK
);

const getListSchema = (schema, { listSchemaPath, listPath }) => ({
    ...schema,
    data: {
        ...schema.data,
        kind: 'list',
    },
    path: listSchemaPath,
    nodepath: listPath,
});

const getListEntrySchema = (
    schema,
    {
        listEntrySchemaPath,
        listEntryPath,
        listEntrySubPath,
    },
) => ({
    ...schema,
    data: {
        ...schema.data,
        kind: 'list-entry',
    },
    relativeKeypath: listEntrySubPath,
    path: listEntrySchemaPath,
    nodepath: listEntryPath,
});

const getListSymlinkSchema = (
    schema,
    {
        listSchemaPath, //      /container-1/choice-1/case-1/list-1
        listRootSchemaPath, //  /container-1
        listSubPath, //         list-1
    },
) => ({
    path: `${listRootSchemaPath}/${listSubPath}`, //    /container-1/list-1
    itemType: getSymlinkItemType(schema),
    symlink: listSchemaPath, //                         /container-1/choice-1/case-1/list-1
    // DEBUG: 'getListSymlinkSchema',
});

const getListEntrySymlinkSchema = (
    schema,
    {
        listEntryPath, //       /container-1/list-1{foo}
        listEntrySchemaPath, // /container-1/choice-1/case-1/list-1{foo}
    },
) => ({
    itemType: getSymlinkItemType(schema),
    path: listEntryPath, //             /container-1/list-1{foo}
    symlink: listEntrySchemaPath, //    /container-1/choice-1/case-1/list-1{foo}
    // DEBUG: 'getListEntrySymlinkSchema',
});


const appendListInCaseSymlinks = schemasList => schemasList.reduce((acc, cur) => {
    if (isList(cur) && cur.relativeKeypath) {
        const listPaths = getListPaths(cur);

        acc.push(getListSchema(cur, listPaths));
        acc.push(getListEntrySchema(cur, listPaths));
        acc.push(getListSymlinkSchema(cur, listPaths));
        acc.push(getListEntrySymlinkSchema(cur, listPaths));
        return acc;
    }
    acc.push(cur);
    return acc;
}, []);

const accumulateRelativeKeypathSymlink = (acc, cur) => {
    const { path, relativeKeypath } = cur;
    const { rootPath } = parsePathByItemType(cur);

    const nodepath = `${rootPath}/${relativeKeypath}`;

    acc.push({
        ...cur,
        nodepath,
    });

    acc.push({
        itemType: getSymlinkItemType(cur),
        path: `${rootPath}/${relativeKeypath}`,
        symlink: path,
        // DEBUG: 'accumulateRelativeKeypath',
    });

    return acc;
};

const createSymlinkMapper = itemType => ({ path, symlink }) => {
    if (itemType === itemTypes.STATIC_SYMLINK) {
        return {
            itemType,
            path: pathWithoutKeyInstances(path),
            symlink: pathWithoutKeyInstances(symlink),
            // DEBUG: 'accumulateCaseAncestorsSymlinks STATIC',
        };
    }
    return {
        itemType,
        path,
        symlink,
        // DEBUG: 'accumulateCaseAncestorsSymlinks EVALUATED',
    };
};

const accumulateCaseAncestorsSymlinks = (acc, cur) => {
    if (isCase(cur)) {
        acc.push(cur);
        return acc;
    }

    if (isChoice(cur)) {
        acc.push(cur);
        return acc;
    }

    const { kindpath } = cur;

    const keypath = KIP.toKeypath(kindpath);
    const itemType = getSymlinkItemType(cur);

    acc.push({
        ...cur,
        nodepath: keypath,
    });

    const symlinks = KIP.getChoiceCaseSymlinks(kindpath);
    acc.push(...symlinks.map(createSymlinkMapper(itemType)));

    return acc;
};

const getChoiceCasePath = (rootPath, ancestorRelativeKeypath, choiceCasePath) => {
    if (ancestorRelativeKeypath === '.') return `${rootPath}/${choiceCasePath}`;
    return `${rootPath}/${ancestorRelativeKeypath}/${choiceCasePath}`;
};

const accumulateCaseSymlink = (acc, cur) => {
    const {
        ancestorRelativeKeypath,
        choiceCasePath,
        path: symlink,
    } = cur;
    const { rootPath } = parsePathByItemType(cur);

    acc.push(cur);

    if (ancestorRelativeKeypath) {
        const path = getChoiceCasePath(rootPath, ancestorRelativeKeypath, choiceCasePath);
        if (path !== symlink) {
            acc.push({
                itemType: getSymlinkItemType(cur),
                path,
                symlink,
                // DEBUG: 'accumulateCaseSymlink',
            });
        }
    }

    return acc;
};

const getChoicePath = (rootPath, ancestorRelativeKeypath, relativeName) => {
    if (ancestorRelativeKeypath === '.') return `${rootPath}/${relativeName}`;
    return `${rootPath}/${ancestorRelativeKeypath}/${relativeName}`;
};

const accumulateChoiceSymlink = (acc, cur) => {
    const {
        path: symlink,
        ancestorRelativeKeypath,
        relativeName,
    } = cur;
    const { rootPath } = parsePathByItemType(cur);


    acc.push(cur);
    if (ancestorRelativeKeypath) {
        const path = getChoicePath(rootPath, ancestorRelativeKeypath, relativeName);
        if (path !== symlink) {
            acc.push({
                itemType: getSymlinkItemType(cur),
                symlink,
                path,
                // DEBUG: 'accumulateChoiceSymlink',
            });
        }
    }
    return acc;
};

const appendSchemaInCaseSymlinks = schemasList => (
    schemasList.reduce((acc, cur) => {
        if (cur.itemType === itemTypes.STATIC_SYMLINK ||
            cur.itemType === itemTypes.EVALUATED_SYMLINK) {
            acc.push(cur);
            return acc;
        }
        const { kindpath } = cur;

        if (isList(cur)) {
            acc.push(cur);
            return acc;
        }

        if (isListEntry(cur)) {
            acc.push(cur);
            return acc;
        }

        if (cur.choicePath) return accumulateChoiceSymlink(acc, cur);
        if (cur.choiceCasePath) return accumulateCaseSymlink(acc, cur);
        if (KIP.isCaseDescendant(kindpath)) return accumulateCaseAncestorsSymlinks(acc, cur);
        if (cur.relativeKeypath) return accumulateRelativeKeypathSymlink(acc, cur);

        acc.push(cur);
        return acc;
    }, [])
);


const evaluatedSchemaFilter = s => (
    s.itemType === itemTypes.EVALUATED_SCHEMA || s.itemType === itemTypes.EVALUATED_SYMLINK
);
const staticSchemaFilter = s => (
    s.itemType === itemTypes.STATIC_SCHEMA || s.itemType === itemTypes.STATIC_SYMLINK
);

const schemasListToObject = schemasList => schemasList.reduce((acc, cur) => {
    const { rootPath, subPath = '.' } = parsePathByItemType(cur);
    const { path, itemType, relativeKeypath, ...schema } = cur;

    if (rootPath in acc && subPath in acc[rootPath].schemas) {
        throw new Error(`State conflict. ['${rootPath}'].schemas['${subPath}'] already exist`);
    }
    assignDeep(acc, { [rootPath]: { schemas: { [subPath]: schema } } });
    return acc;
}, {});

const createProcessSchema = ({
    parser,
    funcs: {
        appendListSymlinksFunc = appendListSymlinks,
        appendListInCaseSymlinksFunc = appendListInCaseSymlinks,
        appendSchemaInCaseSymlinksFunc = appendSchemaInCaseSymlinks,
        appendDependenciesFunc = appendDependencies,
    } = {},
} = {}) => pipe(
    (parent, schema) => parser.parse(parent)(schema),
    flatten,
    appendListSymlinksFunc,
    appendListInCaseSymlinksFunc,
    appendSchemaInCaseSymlinksFunc,
    appendDependenciesFunc,
);

const toSchemasList = (path, schema, options) => {
    const { rootPath } = parsePath(path);
    const ns = getPrefixFromPath(path);
    const parent = { path, ns, data: {}, isRoot: true };

    const parser = createParser(rootPath, options);

    const processSchema = createProcessSchema({ parser });

    return processSchema(parent, schema);
};

export const parseGetSchemaResult = (path, schema, options) => {
    if (isAccessDenied(schema)) return {};

    const schemasList = toSchemasList(path, schema, options);
    const evaluatedSchemas = schemasListToObject(schemasList.filter(evaluatedSchemaFilter));
    const staticSchemas = schemasListToObject(schemasList.filter(staticSchemaFilter));

    return {
        evaluatedSchemas,
        staticSchemas,
    };
};

export const parseGetSchemaError = (path, error) => {
    const { rootPath, rootSchemaPath, subPath } = parsePath(path);

    return {
        evaluatedSchemas: {
            [rootPath]: {
                schemas: {
                    [subPath]: {
                        error,
                    },
                },
            },
        },
        staticSchemas: {
            [rootSchemaPath]: {
                schemas: {
                    [subPath]: {},
                },
            },
        },
    };
};

export {
    accumulateCaseAncestorsSymlinks as _accumulateCaseAncestorsSymlinks,
    appendDependencies as _appendDependencies,
    appendListSymlinks as _appendListSymlinks,
    appendListInCaseSymlinks as _appendListInCaseSymlinks,
    appendSchemaInCaseSymlinks as _appendSchemaInCaseSymlinks,
    createProcessSchema as _createProcessSchema,
    getDependencies as _getDependencies,
    getListSymlinkSchema as _getListSymlinkSchema,
    getListEntrySymlinkSchema as _getListEntrySymlinkSchema,
    getValues as _getValues,
    itemTypes as _itemTypes,
    toSchemasList as _toSchemasList,
};
