import { createSelector } from 'reselect';

import {
    selectHadDataReselect,
} from 'modules/nodes/selectors';
import { selectNsoUser } from 'modules/systemSetting/reducer';
import {
    subscribePath,
} from 'modules/userdata/paths';

const EmptyObject = Object.freeze({});
export const selectUserdata = state => state.userdata || EmptyObject;
export const selectUserMetadata = state => selectUserdata(state).data || EmptyObject;
export const selectColumnsVisibility = (state) => {
    const ustate = selectUserMetadata(state) || EmptyObject;
    return ustate.columnsVisibility || EmptyObject;
};
export const selectVisibilityReselect = createSelector(
    selectColumnsVisibility,
    (_, ownProps) => ownProps.path,
    (nodes, path) => {
        if (!(path in nodes)) {
            return EmptyObject;
        }
        return nodes[path];
    },
);

export const COLUMN_VISIBILITY = {
    VISIBLE_KEY: 'VISIBLE_KEY',
    HIDDEN_USER: 'HIDDEN_USER',
    VISIBLE_USER: 'VISIBLE_USER',
    VISIBLE_DATA: 'VISIBLE_DATA',
    HIDDEN_DATA: 'HIDDEN_DATA',
};

export const createColumnsVisibilitySelector = () => createSelector(
    selectHadDataReselect,
    selectVisibilityReselect,
    (hadData, columnsVisibility) => {
        const columnKeys = [...new Set([
            ...Object.keys(columnsVisibility),
            ...Object.keys(hadData),
        ])];

        return columnKeys.reduce((acc, key) => {
            if (key in columnsVisibility) {
                return {
                    ...acc,
                    [key]: columnsVisibility[key] ?
                        COLUMN_VISIBILITY.VISIBLE_USER : COLUMN_VISIBILITY.HIDDEN_USER,
                };
            }

            return {
                ...acc,
                [key]: hadData[key] ?
                    COLUMN_VISIBILITY.VISIBLE_DATA : COLUMN_VISIBILITY.HIDDEN_DATA,
            };
        }, {});
    },
);

export const selectMetadataPath = (state) => {
    const userName = selectNsoUser(state);
    if (typeof userName === 'string' && userName !== '') {
        return subscribePath(userName);
    }
    return undefined;
};

