import CiscoSansTTLight from '@webui-one/styles/static/CiscoSansTTLight.ttf';

// WARNING: This method uses the @font-face{ src: url()} method with is unsafe.
// The url call is an attack surface for malicious code. Use with care.
// See OWASP for further explanation
// https://www.owasp.org/index.php/XSS_Filter_Evasion_Cheat_Sheet#STYLE_tag_using_background
export const UNSAFECreateFontFace = (context) => {
    const path = context || '';
    if (path === '' || path === 'login/') {
        return (
            `@font-face {
                font-family: 'CiscoSansTT';
                src: url('${path}${CiscoSansTTLight}')  format('truetype');
            }
        `);
    }
    throw new Error('ERROR! Context is not a valid value!');
};


// WARNING: This method uses window.location.href which is an attack surface.
// The purpose of the location.href variable is to redirect the user to local or remote pages.
// The problem arrises since this variable can execute malicsious code by
// assigning javscript: or vbscript: strings to the href variable.
export const UNSAFEWindowLocationHref = (href) => {
    if (!/^(\/|https?:)/i.test(href)) {
        throw Error('invalid url');
    } else {
        window.location.href = href;
    }
};
