import IsomorphicFetch from 'isomorphic-fetch';
import {
    DBS,
    MODES,
    TAGS,
} from './jsonrpc/constants';
import {
    quoteKey,
} from './functional_utils';
import { range, uniq, zipObject } from './utils/array';
import { isDevEnv } from './utils/functional';

let requestId = 0;
let cookie;

const isNode = Object.prototype.toString.call(typeof process !== 'undefined' ? process : 0) === '[object process]';

function JsonRpcError(
    error,
    body,
    { funcs: { isDevMode = isDevEnv } = {} } = {},
) {
    this.message = error.message;
    this.internal = error.internal;
    this.type = error.type;
    this.code = error.code;
    this.data = error.data;
    this.body = body;
    if (isDevMode()) {
        this.stack = (new Error()).stack;
    }
}
JsonRpcError.prototype = Object.create(Error.prototype);

function HttpError(
    method,
    xhr,
    body,
    { funcs: { isDevMode = isDevEnv } = {} } = {},
) {
    this.type = 'http.error';
    this.details = JSON.stringify({ method });
    this.status = xhr.status;
    this.statusText = xhr.statusText;
    this.errorMessage = xhr.responseText;
    this.body = body;
    if (isDevMode()) {
        this.stack = (new Error()).stack;
    }
}
HttpError.prototype = Object.create(Error.prototype);

function getNextRequestId() {
    requestId += 1;
    return requestId;
}

function jsonrpcRequest(method, params, id) {
    return { jsonrpc: '2.0', id, method, params };
}

function newRequest(method, params, id) {
    return JSON.stringify(jsonrpcRequest(method, params, id));
}

let signal;

const getSignal = () => signal;
const setSignal = (newSignal) => { signal = newSignal; };

// NOTE(dbildstr): This will abort pending jsonrpc requests,
// e.g comet, when reloading or navigating to another url.
// Since some browsers throws a NetworkError if there is a
// pending request when navigating it is highly recommended
// to call this function before using the jsonrpc functions.
export const initializeJsonRpc = ({
    Aborter = window.AbortController,
} = {}) => {
    const controller = new Aborter();
    signal = controller.signal;
    setSignal(signal);
    // TODO(dbildstr): ENG-21282: fix or remove aborting jsonrpc requests
    // window.addEventListener('beforeunload', () => {
    //     if (controller) {
    //         controller.abort();
    //     }
    // });
    return {
        controller,
        signal,
    };
};

const jsonrpcCommon = (fetch, body, method) => {
    const baseUrl = isNode ? 'http://localhost:8080' : '';

    const url = `${baseUrl}/jsonrpc/${method}`;
    const headers = {
        Accept: 'application/json;charset=utf-8',
        'Content-Type': 'application/json;charset=utf-8',
    };

    if (isNode && cookie) {
        headers.cookie = cookie;
    }

    return fetch(url, {
        method: 'POST',
        credentials: 'same-origin',
        headers,
        body,
        signal,
    }).then((response) => {
        if (isNode) {
            const setCookie = response.headers.get('set-cookie');
            if (setCookie) {
                cookie = setCookie.split(';')[0];
            }
        }

        if (response.status !== 200) {
            return Promise.reject(new HttpError(method, response, body));
        }
        return response.json();
    });
};

export const jsonrpcBatch = ({
    requests,
    funcs: {
        fetch = IsomorphicFetch,
        getId = getNextRequestId,
    } = {},
} = {}) => {
    // eslint-disable-next-line no-param-reassign
    requests = Array.isArray(requests) ? requests : [requests];
    const body = requests.map(({ method, params }) => jsonrpcRequest(method, params, getId()));
    const methods = requests.map(({ method }) => method);
    const methodsString = uniq(methods).join('-');

    const jsonBody = JSON.stringify(body);

    return jsonrpcCommon(fetch, jsonBody, methodsString);
};

export default function jsonrpcSimple(
    method,
    params,
    {
        funcs: {
            fetch = IsomorphicFetch,
            getId = getNextRequestId,
        } = {},
    } = {},
) {
    const reqId = getId();
    const body = newRequest(method, params, reqId);

    return jsonrpcCommon(fetch, body, method)
        .then((json) => {
            if (json.error) {
                return Promise.reject(new JsonRpcError(json.error, body));
            }
            return json.result;
        });
}

const findThForPath = (trans, path) => trans.reduce((acc, cur) => {
    const { action_path: actionPath } = cur;
    if (actionPath && path.startsWith(actionPath)) return cur.th;
    return acc;
}, undefined);

export const findLastTransaction = (transactions, {
    actionPath = undefined,
    path = undefined,
    tag = TAGS.WEBUI_ONE,
    mode = undefined,
    db = DBS.RUNNING,
} = {}) => {
    if (!transactions) return undefined;
    if (!Array.isArray(transactions)) return undefined;

    if (actionPath) {
        return Object.values(transactions).reduce((acc, cur) => {
            if (cur.action_path === actionPath && cur.db === DBS.CS_TRANS) {
                return cur.th;
            }
            return acc;
        }, undefined);
    }

    return transactions.reduce((acc, cur) => {
        if (path) {
            const thForPath = findThForPath(transactions, path);
            if (thForPath !== undefined) return thForPath;
        }
        if (cur.db !== db) return acc;
        if (tag && cur.tag !== tag) return acc;
        if (mode && cur.mode === mode) return cur.th;
        if (!mode && !acc && cur.mode === MODES.READ) return cur.th;
        if (!mode && cur.mode === MODES.READ_WRITE) return cur.th;
        return acc;
    }, undefined);
};

export const getTrans = async ({
    funcs: {
        jsonrpc = jsonrpcSimple,
    } = {},
} = {}) => jsonrpc('get_trans');

export const getLastTh = async ({
    actionPath,
    path,
    funcs: {
        jsonrpc = jsonrpcSimple,
    } = {},
} = {}) => {
    const transactions = await jsonrpc('get_trans');
    return findLastTransaction(transactions.trans, { actionPath, path });
};

export const getLastReadWriteTh = async ({
    actionPath,
    path,
    funcs: {
        jsonrpc = jsonrpcSimple,
    } = {},
} = {}) => {
    const transactions = await jsonrpc('get_trans');
    return findLastTransaction(transactions.trans, {
        actionPath,
        path,
        mode: MODES.READ_WRITE,
    });
};

export const showConfig = ({
    th,
    path,
    resultAs,
    withOper,
    maxSize,
    funcs: {
        jsonrpc = jsonrpcSimple,
    } = {},
} = {}) => {
    const params = {
        th,
        path,
    };
    if (resultAs) params.result_as = resultAs;
    if (withOper !== undefined) params.with_oper = withOper;
    if (maxSize) params.max_size = maxSize;
    return jsonrpc('show_config', params);
};

