export const getSchemas = ({
    includeSubfolders,
    path,
    paths,
    showEmpty,
    th,
    funcs: {
        jsonrpc = jsonrpcBatch,
    } = {},
}) => {
    let requests = [];
    if (includeSubfolders && showEmpty === true) {
        requests = [{
            method: 'get_schema',
            params: {
                evaluate_when_entries: true,
                levels: -1,
                path,
                stop_on_list: true,
                th,
            },
        }];
    } else {
        // ENG-20233 filter out leafs as they are already included in their parents
        requests = paths.map(p => ({
            method: 'get_schema',
            params: {
                evaluate_when_entries: true,
                levels: 1,
                path: p,
                stop_on_list: false,
                th,
            },
        }));
    }

    return jsonrpc({ requests });
};

export const query = ({
    th,
    selection,
    path,
    sort,
    sortOrder,
    initialOffset,
    chunkSize,
    resultAs = 'leaf_value_as_string',
    xpathExpr,
    contextNode,
    funcs: {
        jsonrpc = jsonrpcSimple,
    } = {},
}) => {
    const params = {
        th,
        path,
        selection,
        result_as: resultAs,
    };

    if (sort !== undefined) params.sort = Array.isArray(sort) ? sort : [sort];
    if (sortOrder !== undefined) params.sort_order = sortOrder;
    if (xpathExpr !== undefined) params.xpath_expr = xpathExpr;
    if (contextNode !== undefined) params.context_node = contextNode;
    if (initialOffset || chunkSize) {
        params.initial_offset = initialOffset || 1;
        params.chunk_size = chunkSize || 10;
    }

    return jsonrpc('query', params);
};

