export const newTrans = async ({
    actionPath,
    db,
    mode,
    tag,
    confMode = 'private',
    funcs: {
        jsonrpc = jsonrpcSimple,
    } = {},
} = {}) => {
    const params = {
        db,
        mode,
        conf_mode: confMode,
    };
    if (actionPath) {
        params.action_path = actionPath;
    }
    if (tag) {
        params.tag = tag;
    }

    return jsonrpc('new_trans', params).then(result => result.th);
};

