export const deleteNodes = async ({
    th,
    paths,
    funcs,
} = {}) => (
    Promise.all(paths.reduce((acc, path) => {
        if (!acc.includes(path)) {
            acc.push(path);
        }
        return acc;
    }, []).map(path => deleteNode({ th, path, funcs })))
);

export function zipQueryResult(selection, objects) {
    if (selection === undefined) {
        if (objects.length === 0) return [];
        return objects.map(o => zipObject(range(0, objects[0].length), o));
    }
    return objects.map(o => zipObject(selection, o));
}

export async function queryAndZipResult({
    th,
    path,
    selection,
    sort,
    sortOrder,
    initialOffset,
    chunkSize,
    funcs: {
        getLastThFunc = getLastTh,
        queryFunc = query,
        zipQueryResultFunc = zipQueryResult,
    } = {},
} = {}) {
    let activeTh = th;
    if (activeTh === undefined) {
        activeTh = await getLastThFunc();
    }
    const params = {
        th: activeTh,
        selection,
        path,
    };
    if (sort !== undefined) params.sort = Array.isArray(sort) ? sort : [sort];
    if (sortOrder !== undefined) params.sortOrder = sortOrder;
    if (initialOffset || chunkSize) {
        params.initialOffset = initialOffset || 1;
        params.chunkSize = chunkSize || 10;
    }
    const response = await queryFunc(params);
    const result = zipQueryResultFunc(selection, response.results);
    return {
        totalNumberOfResults: response.total_number_of_results,
        result,
    };
}

