export const createDisallowExists = async ({
    parentPath,
    values,
    th,
    funcs: {
        createFun = create,
        existsFun = exists,
    } = {},
} = {}) => {
    const validKeyValue = values.map(value => quoteKey(value)).join(' ');
    const path = `${parentPath}{${validKeyValue}}`;
    const { exists: alreadyExists } = await existsFun({ th, path });
    if (alreadyExists) {
        throw Error(`'${validKeyValue}' already exists`);
    }
    await createFun({ th, path });
    return { path };
};

//  "path": <string, default: "/">
//  "format": <"json" | "xml", default: "xml">
//  "mode": <"create" | "merge" | "replace", default: "merge">
export const load = async ({
    data,
    format,
    th,
    mode,
    path,
    funcs: {
        jsonrpc = jsonrpcSimple,
    } = {},
} = {}) => {
    const params = {
        th,
        data,
    };
    if(format) params.format = format;
    if(mode) params.mode = mode;
    if(path) params.path = path;
    return jsonrpc('load', params);
};

