import {
    pathWithoutKeyInstances,
} from '@webui-one/core/keypath/kp-parser';

import { toNedKindpath } from '@webui-one/core/kindpath';

import { itemTypes } from '../schemas';
import { getName } from '../utils';

const createStaticSchemaData = (schema) => {
    const {
        access,
        active,
        evaluated_when_entry, // eslint-disable-line camelcase
        exists,
        leafrefGroups,
        leafref_groups, // eslint-disable-line camelcase
        children,
        cases,
        value,
        ...rest
    } = schema.data;
    return rest;
};

const deviceConfigChildRE = new RegExp('^/ncs:devices/device{[^}]*}/(config/([^:]+):.*)');

const createStaticSchemaPath = (path) => {
    const keyInstancePath = pathWithoutKeyInstances(path);
    const match = deviceConfigChildRE.exec(keyInstancePath);

    if (match !== null) {
        const nedPath = `/ned-id:${match[2]}/${match[1]}`;
        return nedPath;
    }
    return keyInstancePath;
};

export const createStaticSchema = (rawSchema) => {
    const path = createStaticSchemaPath(rawSchema.path);

    const { hasEvaluatedWhenEntries, kindpath, ...rest } = rawSchema;

    const schema = {
        ...rest,
        path,
        data: createStaticSchemaData(rawSchema),
        itemType: itemTypes.STATIC_SCHEMA,
    };
    if (kindpath) schema.kindpath = toNedKindpath(kindpath);

    if (rawSchema.data.children && rawSchema.data.children.length > 0) {
        schema.children = rawSchema.data.children.map(c => getName(path)({ data: c }));
    }

    return schema;
};

export {
    createStaticSchemaPath as _createStaticSchemaPath,
    createStaticSchemaData as _createStaticSchemaData,
};
