const parseNativeCommitDryRunResult = result => (
    result.reduce((acc, cur, ix) => {
        const res = acc;
        if ((ix % 2) === 0) {
            res.push({ device: cur.value, text: '' });
        } else {
            res[res.length - 1].text = cur.value;
        }
        return res;
    }, [])
);


