/* eslint-disable import/prefer-default-export */
import { isObject } from '@webui-one/core/utils/object';

export const authenticatedKeys = [
    'includeSubfolders',
    'isEditMode',
    'lastLocation',
    'showActions',
    'showConfig',
    'showEmpty',
    'showOperdata',
];

export const nonAuthenticatedKeys = [
    'includeSubfolders',
    'showActions',
    'showConfig',
    'showEmpty',
    'showOperdata',
];

export const applicationConfigurationFilter = ({ state, options }) => {
    const validKeys = isObject(options) && options.authenticated
        ? authenticatedKeys
        : nonAuthenticatedKeys;
    return Object.entries(state).reduce((values, [key, value]) => {
        if (validKeys.includes(key)) {
            Object.assign(values, { [key]: value });
        }
        return values;
    }, {});
};

