import * as flagTypes from 'modules/flags/flagTypes';
import { flagIsSet } from 'modules/flags';
import { isDevEnv, isFunction } from '@webui-one/core/utils/functional';

const userTiming = () => next => (action) => {
    if (!isDevEnv() || !flagIsSet(flagTypes.USER_TIMING_ENABLE, 'true')) {
        return next(action);
    }
    if (!performance || !isFunction(performance.mark)) return next(action);
    performance.mark(`${action.type}_start`);
    const result = next(action);
    performance.mark(`${action.type}_end`);
    performance.measure(
        `${action.type}`,
        `${action.type}_start`,
        `${action.type}_end`,
    );
    return result;
};

export default userTiming;
