export const isFunction = func => typeof func === 'function';

export const isDevEnv = () => (process.env.NODE_ENV === 'development');

export const defaultTo = (defaultValue, value) => (
    (value == null || Number.isNaN(value)) ? defaultValue : value
);

export const unzipRow = (selection, rows) => {
    if (selection.length > 0 && rows.length > 0) {
        return rows.map(row => (
            selection.map(prop => (row[prop]))
        ));
    }
    return [];
};

export const compose = (...fns) => fns.reduce((f, g) => (...args) => f(g(...args)));
export const pipe = (...fns) => compose.apply(compose, fns.reverse());
